cc.requestLoginResult = function (code) {
    if (code) {
        let xmlHttp = cc.loader.getXMLHttpRequest();
        xmlHttp.open("POST", PayServerAddr + 'wx_Nativelogin', true);
        xmlHttp.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        xmlHttp.onreadystatechange = function () {
            if (this.readyState == 4 && this.status == 200) {
                var result = JSON.parse(xmlHttp.responseText);//ajax
                appFacade.sendNotification(FishConstants.ON_GameLobbyLoginUI_Nativelogin, result);
            }
        }
        xmlHttp.timeout = 5000;// 5 seconds for timeout
        xmlHttp.send(JSON.stringify({ code: code }));
    }
    else {
        //UIManager.openTip("")
    }
};
