
cc.Class({
    extends: cc.Component,

    properties: {

    },


    onLoad () {

        cc.game.addPersistRootNode(this.node)
        
        cc.systemEvent.setAccelerometerEnabled(true); // 
        cc.systemEvent.on(cc.SystemEvent.EventType.DEVICEMOTION, function (event) {

            let s1 = cc.view.getFrameSize(); //
            let s2 = cc.view.getDesignResolutionSize(); //
    
            let game_set = {}
            game_set.real_size = {}
            let ttt= s1.width/s1.height;
            if(ttt >= s2.width/s2.height){
                //  ,
                game_set.real_size.h = s2.height;
                game_set.real_size.w = s2.height*ttt;
    
            }else{
                //4:3  
                game_set.real_size.h = s2.width/ttt;
                game_set.real_size.w =s2.width;
    
            }

            let node = cc.director.getScene().getChildByName("Fillbackground");
    
            node.setScale(4)
            node.setContentSize(game_set.real_size.w, game_set.real_size.h); 
            //this.node.setContentSize(game_set.real_size.w, game_set.real_size.h); 
        });
        
    },

    start () {

    },

    // update (dt) {},



});
