let GameProxy = require('GameProxy');
let GameProtocol = require('GameProtocol');
let datatypes = GameProtocol.datatypes;
let constVal = GameProtocol.constVal;
let GameModel_520 = require('gameModel_520');

let GameConfig = require('gameConfig').config;

const i18n = require('i18n');

cc.Class({

    extends: GameProxy,

	onRegister: function () {
        this.gameModel_520 = GameModel_520
		this.gameConfig = GameConfig;
		this._globalDataProxy = this.facade.retrieveProxy('FishGlobalDataProxy');
		this._super();
	},

	registerNetInterface: function (app,gameinfo) {

		this._super(app,gameinfo);

        //////////////////////////////////  ///////////////////////////////////////

        //
        this.net.registerClient(this, 'C_gameStart', datatypes.CMD_C_GameStart);

        //
        this.net.registerClient(this, 'GameConclude', datatypes.CMD_GF_Game_Conclude);
        
        //
        this.net.registerClient(this, 'freeSpinStart', datatypes.CMD_C_GAME_AWAY_START);
        
        //
        this.net.registerClient(this, 'requestSenceChoose', datatypes.CMD_C_SenceChoose); 
        //
        this.net.registerClient(this, 'requestRewardChoose', datatypes.CMD_C_RewardChoose); 
        //
        this.net.registerClient(this, 'requestMailBoxReward', datatypes.CMD_C_MailBoxReward); 
        //
        this.net.registerClient(this, 'requestBetRewardPool', datatypes.CMD_C_BetRewardPool); 
		
		//////////////////////////////////  ///////////////////////////////////////

        //
        this.net.registerServer(this, 'onGameMessageGameStart', datatypes.CMD_S_GameStart);

        //
        this.net.registerServer(this, 'onGameMessageGameConclude', datatypes.CMD_S_Game_Conclude); 

		this.net.registerServer(this, 'onGameMessageSendmailBox', datatypes.CMD_S_SendmailBox);

        this.net.registerServer(this, 'onEventGameSceneStop', datatypes.CMD_S_Scence_Stop);

        //
        this.net.registerServer(this, 'onGameMessageSenceChoose', datatypes.CMD_S_SenceChoose);
        //
        this.net.registerServer(this, 'onGameMessageRewardChoose', datatypes.CMD_S_RewardChoose);
        //
        this.net.registerServer(this, 'onGameMessageMailBoxReward', datatypes.CMD_S_MailBoxReward);
        //
        this.net.registerServer(this, 'onGameMessageLockMailBox', datatypes.CMD_S_LockMailBox);
        //
        this.net.registerServer(this, 'onGameMessageBetRewardPool', datatypes.CMD_S_BetRewardPool);
        

		////////////////////////////////////////////////////////////////////////////////////



	},

    ////////////////////////////////// network //////////////////////////////////

	// spin
	requestSpin(lineNum, betNum){
        if (this.gameModel._freeSpin > 0) {
            this.net.callServer('freeSpinStart');
        }
        else{
            this.net.callServer('C_gameStart',lineNum,betNum - 1);
        }

        
        //
        if (this._gameView && this._gameView.cleanWildStickItems) {
            this._gameView.cleanWildStickItems(this.gameModel_520.wildList)
        }

        //
        if (this.gameModel_520.wildList && this._gameView && this._gameView.drawWildStickItems) {
            this._gameView.drawWildStickItems(this.gameModel_520.wildList)
            this.gameModel_520._wildList = this.gameModel_520.wildList
        }
	},

    // @override 
    requestStop(){
        if (this.gameModel._freeSpin > 0) {
            this.sendUserReady()
        }
        else{ 
            this.net.callServer('GameConclude');
        }
    },



    //  
    requestSenceChoose(num){
        cc.log("")
        this.net.callServer('requestSenceChoose',num);
    },

    //  
    requestRewardChoose(num){
        cc.log("")
        this.net.callServer('requestRewardChoose',num);
    },

    //  
    requestMailBoxReward(id){
        cc.log("")
        this.net.callServer('requestMailBoxReward',id);
    },

    //   
    requestBetRewardPool(num){
        cc.log("")
        this.net.callServer('requestBetRewardPool',num);
    },
    

    //
    onGameMessageGameConclude:function(){
        cc.log("")
        this.sendUserReady()
        this.dealRequestStopResult()
    },
    
    //
    onGameMessageGameStart:function(_data){

        let data = _data
        cc.log("",data);
        this.gameModel_520._CanBn = data.bCanBn
        this.gameModel_520._isPlayWaitAni = true     //wait
        let ret = {}

        let winPos = []
        let hash = {}
        for (let [index,type] of Object.entries(data["cbItemType"])){
            if (type != 255) {
                let line = this.gameModel.lines[index]
                for (let i = 0;i < data["cbLineCount"][index];i++) {
                    let way = i + "." + line[i]
                    hash[way] = true
                }
            }
        }

        for (let [k, v] of Object.entries(hash)) {
            let p = k.split(".");
            winPos.push([Number(p[0]),Number(p[1])])
        }

        if (this.gameModel_520.wildList) {
            for (let i = 0;i < 5;i++) {
                for (let j = 0;j < 3;j++) {
                    if (this.gameModel_520.wildList[i][j]===0) {
                        if (data["ItemInfo"][j][i]===0) {
                            data["ItemInfo"][j][i] = Help.getRandomInt(2,10)
                        }
                    }                   
                }
            }
        }

        this.gameModel_520._GiveAwayCount = data["nGiveAwayCount"]

        //
        if (data["nGiveAwayCount"] > 0) {
            for (let i = 0;i < constVal.ITEM_X_COUNT;i++)  {
                for (let j = 0;j < 3;j++) {
                    if (data["ItemInfo"][j][i]===1) {
                        winPos.push([i, j - 1])
                    }
                }
            }
        }

        ret["win_pos"] = winPos
        
        let winLine = []
        for (let [index, type] of Object.entries(data["cbItemType"])) {
            if (type != 255) {
                let temp = {}
                temp["line"] = index
                temp["symbol"] = type
                temp["count"] = data["cbLineCount"][index]
                winLine.push(temp)
            }
        }

        ret["win_lines"] = winLine

        let itemList = []
        for (let i = 0;i < constVal.ITEM_X_COUNT;i++)  {
            let temp = []
            for (let j = 0;j < 3;j++) {
                temp[j] = data["ItemInfo"][j][i]
            }
            itemList[i] = temp
        }
        //itemList = [[8,10,0],[5,0,2],[7,1,9],[0,7,8],[7,8,9]]

        ret["item_list"] = itemList

        if (this._gameView.isOneFree) {
            this.gameModel_520._recoveryFreeList = ret["item_list"]
        }

        let totalWin = 0
        for (let i = 0;i < constVal.MAX_LINE_COUNT;i++) {
            totalWin = totalWin + data["dLineScore"][i]
        }

        ret["total_win"] = totalWin

        ret["free_spin"] = data["nTotalGiveAwayCount"]
        ret["free_win"] = data["dUserTotalWinScore"]
        
        ret["free_state"] = data["cbFreeSpinState"]
        cc.log(ret.item_list)
        cc.log(ret["win_lines"])
        
        this.dealRequestSpinResult(ret)

        //
        this.gameModel_520.wildList = []
        for (let i = 0;i < constVal.ITEM_X_COUNT;i++)  {
            let temp = []
            for (let j = 0;j < 3;j++) {
                if (data["WildStickInfo"][j][i]===0) {
                    //
                    temp[j] = data["WildStickInfo"][j][i]
                }
            }
            this.gameModel_520.wildList[i] = temp
        } 
    },

    //
    onGameMessageSendmailBox:function(data){

        //cc.log(""+data);
        this.sendNotification("SUB_S_SEND_MAILBOX",data);
            
    },

    onEventGameSceneStop: function () {
        cc.log("CMD_S_Scence_Stop");
    },

    onGameMessageSenceChoose: function (_data) {
        cc.log("onGameMessageSenceChoose");

        //
        setTimeout(function(){
            this._gameView.BonusLayer.PlayQiePingAni()
        }.bind(this), 1000)

    },
    onGameMessageRewardChoose: function (_data) {
        cc.log("onGameMessageRewardChoose");

        let cmd_table = _data
  
        //(1-3)  //1//id
        let RewardGoodsID = cmd_table.bRewardGoodsID
        // 
        let RewardValue = cmd_table.llRewardValue
        //id
        let GoodsID = cmd_table.cbGoodsID
    
        for (let i = 0;i < 4;i++) {
            this._gameView.BonusLayer.SetJackpot(i,cmd_table.stSendMainbox[i])
        }
        
        this._gameView.BonusLayer.CreateGoods(RewardGoodsID,RewardValue,GoodsID)
    },
    onGameMessageMailBoxReward: function (_data) {
        cc.log("onGameMessageMailBoxReward");
        let cmd_table = _data
        this._gameView.BonusLayer.KaiBaoXianXiang(cmd_table.llRewardValue,cmd_table.cdRewardID)
    },
    onGameMessageLockMailBox: function (_data) {
        cc.log("onGameMessageLockMailBox");
        let cmd_table = _data
        this.gameModel_520._UnlockRewardID = cmd_table.bUnlockRewardID
        this._gameView.BonusLayer.SetjiangchiAni(cmd_table.bUnlockRewardID ) 
    },
    onGameMessageBetRewardPool: function (_data) {
        cc.log("onGameMessageBetRewardPool");
        let cmd_table = _data
        this.gameModel_520._UnlockRewardID = cmd_table.bUnlockRewardID
        this._gameView.ShowJackpotKuang(cmd_table.bUnlockRewardID)
        this._gameView.SetJackpotText(cmd_table.stSendMainbox)
    },

    //
    onEventGameScene: function (gameStatus) {
		this._super()
        let data = null;
        if(gameStatus===constVal.GAME_SCENE_FREE || gameStatus===constVal.GAME_SCENE_WAIT){
                
            data = datatypes.CMD_S_StatusFree.createFromStream(this.app.stream);
            this.gameModel_520._UnlockRewardID = data.bUnlockRewardID

        }else if(gameStatus===constVal.GAME_SCENE_PLAY){
            data = datatypes.CMD_S_StatusPlay.createFromStream(this.app.stream);
        };
            
        if (null===data) {
            return
        }

        let ret = {}
        ret["bet_index"] = data["cbBetIndex"] + 1
        ret["baseScore"] = data["lCellScore"]
        ret["betCount"] = data["cbBetCount"]
        ret["betList"] = data["lBetScoreArr"]

        // 
        if (data["ItemInfo"]) {
            let winPos = []
            let hash = {}
            for (let [index,type] of Object.entries(data["cbItemType"])){
                if (type != 255) {
                    let line = this.gameModel.lines[index]
                    for (let i = 0; i < data["cbItemLineCount"][index];i++) {
                        let way = i + "." + line[i]
                        hash[way] = true
                    }
                }
            }

            for (let [k, v] of Object.entries(hash)) {
                let p = k.split(".");
                winPos.push([Number(p[0]),Number(p[1])])
            }
            ret["win_pos"] = winPos
            
            let winLine = []
            for (let [index, type] of Object.entries(data["cbItemType"])) {
                if (type != 255) {
                    let temp = {}
                    temp["line"] = index
                    temp["symbol"] = type
                    temp["count"] = data["cbLineCount"][index]
                    winLine.push(temp)
                }
            }
    
            ret["win_lines"] = winLine


            let itemList = []
            for (let i = 0;i < constVal.ITEM_X_COUNT;i++)  {
                let temp = []
                for (let j = 0;j < 3;j++) {
                    temp[j] = data["ItemInfo"][j][i]
                }
                itemList[i] = temp
            }
    
            ret["item_list"] = itemList


            this.gameModel_520._recoveryFreeList = ret["item_list"]

            let totalWin = 0
            for (let i = 0;i < constVal.MAX_LINE_COUNT;i++) {
                totalWin = totalWin + data["lLineScore"][i]
            }
    
            ret["total_win"] = totalWin

            ret["free_spin"] = data["nTotalGiveAwayCount"]
            ret["free_win"] = data["dUserTotalWinScore"]
            
            ret["free_state"] = data["cbFreeSpinState"]

            ret["line_count"] = data["cbLineCount"]
            ret["bet_index"] = data["cbBetIndex"] + 1

            this.gameModel_520._CanBn             = data.bCanBn
            this.gameModel_520._VerifyMailBox     = data.bVerifyMailBox
            this.gameModel_520._BnSenceId         = data.bBnSenceId
            this.gameModel_520._bRewardGoodChoose = data.bRewardGoodChoose
            this.gameModel_520._UnlockRewardID    = data.bUnlockRewardID
            this.gameModel_520._RewardNum         = data.llRewardNum
            this.gameModel_520._SkullNum          = data.bSkullNum        
            this.gameModel_520._isBreakLine       = true
        }

        this.dealSceneData(ret)

	},

    // @override
    createStartFreeLayer(){

        let startFree = function(){
            this.freeGameStart()
        }.bind(this)

        if (this._gameView && this._gameView.createStartFreeLayer) {
            this._gameView.createStartFreeLayer(this.gameModel._freeSpin, startFree)
        }

    },

    // @override 
    playReelNotifyEffect(col){
    
        if (this.gameModel_520._isPlayWaitAni===false) {
            return
        }
        //todo
        //this._gameView:playBonus_lzjl(col,this.speedUpState[col])

    },

    // @override 
    onReelStop(col){
        if (this.notifyState && this.notifyState[col] && this.notifyState[col].length > 0) {
            this.playSpecialSymbolStopAni(col, this.notifyState[col])
    
            if (this.checkSpeedUp(col + 1)) {
                
                this.playReelNotifyEffect(col + 1)  
                this.gameModel_520._isPlayWaitAni = false
            }        
    
        }
    },

    recoveryFreeScene(){
        if (this._gameView && this._gameView.cleanWildStickItems) {
            this._gameView.cleanWildStickItems(this.gameModel_520.wildList)
            if (this._gameView.animateNode) {
                this._gameView.animateNode.removeAllChildren()
            }
        }

        for (let [col,keys] of Object.entries(this.gameModel_520._recoveryFreeList)){
            for (let [row,key] of Object.entries(keys)){
                let cell = this._gameView.getCurSpinLayer().getCell(col, row)
                cell.active = true
                this.updateCellSprite(cell, col, true, key)
            }
        }
    },

    // @override 
    handleResult(){

        this.drawAnimate(this.gameModel._spinResult)

        this.sendNotification("CHANGE_SPIN_BTN_STATE",true);

        let callback = function (){
            
            if (this.gameModel._freeState >= 1 && this.gameModel._freeState <= 2 && this.gameModel._freeSpin > 0) {
                
                this._gameView.scheduleOnce(function(){
                    this.createStartFreeLayer()
                }.bind(this), 1)
                
            }
            else if (this.gameModel._freeState===3 && this.gameModel._freeSpin < 1) {

                    //spin
                    let delay = 0
                    if (this.gameModel._totalWin > 0) {
                        delay = 1
                    }

                    this.gameModel._curwinscore = this.gameModel._freewin

                    this._gameView.scheduleOnce(function(){
                        //this._gameLayer:setFreeGameFireVisible(false)
                        this.createFreeRewardLayer()
                    }.bind(this), delay)


            }

            // 
            if (this.gameModel._freeSpin < 1 && this._freeState != 3) {

                //spin
				this.sendNotification("SET_TOTAL_WIN",this.gameModel._totalWin);
				this.sendNotification("UPDATE_SCORE",this.gameModel._totalWin);
				this.sendNotification("CHANGE_GAME_BTN_STATE",true);
				this.gameModel._curwinscore = this.gameModel._totalWin

            }
            else{
                this.sendNotification("SET_TOTAL_WIN",this.gameModel._freewin);
            }
            
            if (this.gameModel._freeState != 3) {
                this.requestStop()
            }
            

        }.bind(this)


        //BN
        if (this.gameModel_520._CanBn) {
            //
            if (this.gameModel_520._BnSenceId===255) {
                setTimeout(function(){
                    this._gameView.createBonusLayer(callback)
                }.bind(this), 4000)
            }     
            else{
                this._gameView.RecoverBonusLayer(callback,this.gameModel_520._BnSenceId + 1,this.gameModel_520._VerifyMailBox,this.gameModel_520._bRewardGoodChoose,this.gameModel_520._UnlockRewardID ,this.gameModel_520._RewardNum,this.gameModel_520._SkullNum)          
            }
        }
        else{

            callback()

        }

        this.gameModel_520._isBreakLine       = false
    },

    //@override 
    drawAnimate(data){

        this.gameModel_520._CanFree = false

        if (this.gameModel_520._CanBn) {
            this.gameUIManager.playAudio("bell",false)
            this.gameUIManager.playAudio("anti_suc_vo1",false)
            this._gameView.animateNode.removeAllChildren()
            //
            for (let i = 0;i < 5;i++) {
                if (this.notifyState && this.notifyState[i] && this.notifyState[i].length > 0) {
                    this._gameView.playSpecialSymbolStopAni(i, this.notifyState[i],this.gameModel_520._CanBn)
                }             
            }
        }

        
        //
        if (data["win_pos"] && data["win_pos"].length > 0) {
            //
            if (this.gameModel_520._GiveAwayCount > 0) {
                this.gameModel_520._CanFree = true
                this.gameUIManager.playAudio("bell",false)
                this.gameUIManager.playAudio("anti_suc_vo2",false)
                this._gameView.animateNode.removeAllChildren()
                //
                for (let i = 0;i < 5;i++) {
                    if (this.notifyState && this.notifyState[i] && this.notifyState[i].length > 0) {
                        this._gameView.playSpecialSymbolStopAni(i, this.notifyState[i])
                    }             
                }
            }
            
            this.gameUIManager.playAudio("win1",false)
            setTimeout(function(){
                this.gameUIManager.playAudio("win1end",false)
            }.bind(this), 1000)


            this.drawLinesThemeAnimate(this.gameModel.winLines, this._gameView.getCurSpinLayer(), data)
        }

        //item
        if (this.gameModel_520._wildList && this._gameView && this._gameView.hideWildStickDownItems) {
            this._gameView.hideWildStickDownItems(this.gameModel_520._wildList)
        }

   
    },

    //@override 
    subBet(){
        if (this.gameModel._betIndex <= 1) {
            this.gameModel._betIndex = this.gameModel._betCount
        }
        else{
            this.gameModel._betIndex = this.gameModel._betIndex - 1
        }
    
        this.requestBetRewardPool(this.gameModel._betIndex - 1)
        
        this.sendNotification("SET_TOTAL_BET",this.gameModel.getTotalBet());
    },
    
    //@override 
    addBet(){
        if (this.gameModel._betIndex >= this.gameModel._betCount) {
            this.gameModel._betIndex = 1
        }
        else{
            this.gameModel._betIndex = this.gameModel._betIndex + 1
        }
    
        this.requestBetRewardPool(this.gameModel._betIndex - 1)
       
        this.sendNotification("SET_TOTAL_BET",this.gameModel.getTotalBet());
    },
    
    
    //@override 
    maxBet(){
        this.gameModel._betIndex = this.gameModel._betCount
        this.requestBetRewardPool(this.gameModel._betIndex - 1)
        this.sendNotification("SET_TOTAL_BET",this.gameModel.getTotalBet());
    },

    freeGameStart(){
        // this._gameLayer:setFreeGameFireVisible(false)
        if (this._gameView && this._gameView.setFreeRemain) {
            this._gameView.setFreeRemain(this.gameModel._freeSpin - 1)
        }
        this.spin()
    },	

})

