let appFacade = require('FishFacade');
let ReelStatus = {
    ["start"]           : 1,
    ["running"]         : 2,
    ["stopping"]        : 3,
    ["downbounce"]      : 4,
    ["stop"]            : 5
}

let GameSpinReel = cc.Class({
    extends: cc.Node,

    /*[[
        
    
        @param colid        id 
        @param spinLayer    
        @param reelNode     , 
        @param reelConfig   (, , , )
    ]]*/
    ctor:function () {
        let colid = arguments[0]
        let spinLayer = arguments[1]
        let reelNode = arguments[2]
        let reelConfig = arguments[3]
        let gameViewLayer = arguments[4]

        this.colid              = colid                                     // 
        this.spinLayer          = spinLayer                                 // 
        this.reelNode           = reelNode                                  // 
        this.row                = reelConfig["symbolCount"]                 // 
        this.basePos            = reelConfig["base_pos"]                    // 
        this.adjustPos          = reelConfig["adjust_pos"]                  // 
        this.cellWidth          = reelConfig["cellWidth"]                   // 
        this.cellHeight         = reelConfig["cellHeight"]                  // 
        this.addBelow           = 3                                         // 
        this.addTop             = 1                                         // 
        this.number             = this.addBelow + this.row + this.addTop    // 
        this.maxIndex           = this.number - 1                           // id
        this.minIndex           = 0                                         // id
        this.lastIndex          = this.minIndex                             // id
        this.currentIndex       = this.row + this.addBelow - 1              // id()
        this.actionQueue        = []                                        // 
        this.curAction          = null                                       // 
        this.curStatus          = ReelStatus.stop                           // 
        this.itemList           = null                                       // 
        this.ExtraSymbolCount   = 6                                         // 
        this.width              = this.cellWidth                            // 
        this.height             = this.cellHeight * this.number             // 
        this.cells              = {}                                        // 


        this.setContentSize(cc.v2(this.width, this.height))

        this.gameCtrl           = appFacade.retrieveProxy('GameProxy')
        this.gameViewLayer      = gameViewLayer
        this.initCell()
    },

    initCell(){
        for (let i = this.minIndex; i <= this.maxIndex;i++) {
            let theCell = this.gameViewLayer.createCellSprite(this.colid, i)
            if (theCell) {
                let adjustPos = cc.v2(0.5 * this.cellWidth, (i - this.addBelow + 0.5) * this.cellHeight)
                let cellPos = (this.basePos.add(adjustPos)).add(this.adjustPos)
                theCell.setPosition(cellPos)
                this.reelNode.addChild(theCell)
                this.cells[i] = theCell
            }
        }
    },

    resetCol(){
        this.curStatus      = ReelStatus.stop
        this.lastIndex      = this.minIndex
        this.currentIndex   = this.row + this.addBelow - 1
        this.curSpeed       = 0
        this.curAction      = null
        this.isReelStoping  = false

        this.clearActions()
        this.resetCellsPosition()
    },

    resetCellsPosition(){
        for (let index = this.minIndex; index < this.maxIndex;index++) {
            let theCell = this.cells[index]
            let adjustPos = cc.v2(0.5 * this.cellWidth, (index - this.addBelow + 0.5) * this.cellHeight)
            let cellPos = (this.basePos.add(adjustPos)).add(this.adjustPos);
            theCell.setPosition(cellPos)
        }

    },

    /*
        , 

        action = {
            beginFun            
            endFun              
            totalTime           
            endSpeed            
            accelerationTime    
            delay               cell
        }
        @param dt 
    */
    updateCallFunc(dt){
        //cc.log(dt)
        if (this.onDelayStopSchedule) {
            this.delayTotalTime = this.delayTotalTime + dt
            
            if (this.delayTotalTime > this.stopConfig.stopDelay) {
                //todo
                if (this.gameCtrl.checkSpeedUp(this.colid)==false) {
                    this.onDelayStopSchedule = false
                    this.onReelStartStop()                    
                }        
                
            }
        }
        if (!this.curAction && this.actionQueue.length > 0) {
            this.curAction = this.actionQueue.shift()
            this.currentTime = 0
            this.acceleration = null

            if (this.curAction.beginFun) {
                this.curAction.beginFun(this.colid)
            }
        }

        if (this.curAction) {
            this.refresh(dt)
        }
    },

    /*
        cell
    
        , (index + 1)
        
    
        @param dt 
    */
    refresh(dt){
        this.currentTime = this.currentTime + dt
        let timeLeft = 0
        let isCurrentActionEnd = false
    
        // 
        if (this.currentTime >= this.curAction.totalTime) {
            timeLeft = this.currentTime - this.curAction.totalTime
            isCurrentActionEnd = true
            dt = dt - timeLeft
        }
    
        if (!this.curAction.delay) {
            let moveY = this.getMoveY(dt)
            for (let [_,theCell] of Object.entries(this.cells)){
                theCell.y = theCell.y + moveY
            }
    
            let _currentCell = this.getCurrentCell()
            while (_currentCell.y < (this.basePos.y + this.adjustPos.y) + (this.row) * this.cellHeight) {
                this.currentIndex = this.increaseIndex(this.currentIndex)
                //todo
                this.gameCtrl.updateCellSprite(this.getNextCell(), this.colid, this.isReelStoping)
                _currentCell = this.getCurrentCell()
            }
        }
    
        if (isCurrentActionEnd) {
            if (this.curAction.endFun) {
                this.curAction.endFun(this.colId)
            }
            this.curAction = null
        }
    
        if (timeLeft > 0) {
            this.updateCallFunc(timeLeft)
        }
    },

    /*
        , 
    
        @param dt 
    */
    getMoveY(dt){
        if (!this.acceleration) {
            if (!this.curSpeed) {
                this.curSpeed = 0
            }
            if (this.curAction.endSpeed != this.curSpeed && this.curAction.accelerationTime) {
                this.acceleration = (this.curAction.endSpeed - this.curSpeed) / this.curAction.accelerationTime
            }
            else{
                this.acceleration = 0
                this.curSpeed = this.curAction.endSpeed
            }
        }
    
        let tempSpeed = this.curSpeed + this.acceleration * dt
        if (this.acceleration > 0) {
            if (tempSpeed > this.curAction.endSpeed) {
                tempSpeed = this.curAction.endSpeed
                this.acceleration = 0
            }
        }
        else if (this.acceleration < 0) {
            if (tempSpeed < this.curAction.endSpeed) {
                tempSpeed = this.curAction.endSpeed
                this.acceleration = 0
            }
        }
    
        let moveDis = (tempSpeed + this.curSpeed) / 2 * dt
        this.curSpeed = tempSpeed
        return moveDis
    },

    setCellStatus(visible){
        for (let [_,theCell] of Object.entries(this.cells)){
            theCell.active = visible;
        }
    },

    getRetCell(index){
        let cellIndex = this.currentIndex - index + 1
        if (cellIndex < 0) {
            cellIndex = cellIndex + this.number
        }
        else if (cellIndex > this.number - 1) {
            cellIndex = 0
        }
        return this.cells[cellIndex]
    },

    getCurrentCell(){
        return this.cells[this.currentIndex]
    },

    getNextCell(){
        let tempIndex = this.currentIndex + 1
        if (tempIndex > this.number - 1) {
            tempIndex = 0
        }
        return this.cells[tempIndex]
    },

    getFirstCell(){
        let firstIndex = this.lastIndex - 1
        if (firstIndex < 0) {
            firstIndex = this.number - 1
        }
        return this.cells[firstIndex]
    },

    getLastCell(){
        return this.cells[this.lastIndex]
    },
    
    increaseIndex(index){
        let tempIndex = index + 1
        if (tempIndex > this.number - 1) {
            tempIndex = 0
        }
        let firstCell = this.getFirstCell()
        let lastCell = this.getLastCell()
        lastCell.y = firstCell.y + this.cellHeight
        this.lastIndex = this.lastIndex + 1
        if (this.lastIndex > this.number - 1) {
            this.lastIndex = 0
        }
        return tempIndex
    },

    decreaseIndex(index){
        let tempIndex = index - 1
        if (tempIndex < 0) {
            tempIndex = tempIndex + this.number
        }
        let firstCell = this.getFirstCell()
        let lastCell = this.getLastCell()
        firstCell.y = lastCell.y - this.cellHeight
        this.lastIndex = this.lastIndex - 1
        if (this.lastIndex < 0) {
            this.lastIndex = this.number - 1
        }
        return tempIndex
    },

    spin(spinConfig){
        if (spinConfig.locked) {
            this.curStatus = ReelStatus.stop
            this.locked = true
            return null
        }

        if (this.curStatus != ReelStatus.stop) {
            return null
        }

        this.locked = false
        this.start(spinConfig)
        this.curStatus = ReelStatus.running
    },

    start(spinConfig){
        this.itemList = null
        this.curStatus = ReelStatus.start
        this.spinConfig = spinConfig
        this.onDelayStopSchedule = false
        this.actionQueue = []
        this.parseStart(spinConfig)

    },

    parseStart(spinConfig){
        if (spinConfig.upBounceTime > 0) {
            let tAcc = (spinConfig.upBounceMaxSpeed * spinConfig.upBounceTime - spinConfig.upBounce) / spinConfig.upBounceMaxSpeed
            let tMax = spinConfig.upBounceTime - 2 * tAcc
            this.actionQueue.push({["delay"] : 1, ["totalTime"] : spinConfig.delay});
            this.actionQueue.push({["endSpeed"] : spinConfig.upBounceMaxSpeed, ["totalTime"] : tAcc, ["accelerationTime"] : tAcc});
            this.actionQueue.push({["endSpeed"] : spinConfig.upBounceMaxSpeed, ["totalTime"] : tMax});
            this.actionQueue.push({["endSpeed"] : 0, ["totalTime"] : tAcc, ["accelerationTime"] : tAcc});
        }
        this.actionQueue.push({["endSpeed"] : spinConfig.maxSpeed, ["totalTime"] : spinConfig.speedUpTime, ["accelerationTime"] : spinConfig.speedUpTime});
        this.actionQueue.push({["endSpeed"] : spinConfig.maxSpeed, ["totalTime"] : 10000});
    },

    stop(stopConfig, colItemList, upitem, downlist){
        if (this.curStatus != ReelStatus.running) {
            return null
        }
        this.parseStop(stopConfig, colItemList, upitem, downlist)
    },

    parseStop(stopConfig, colItemList, upitem, downList){
        if (stopConfig.ClearAction) {
            this.clearActions()
        }
        this.stopConfig = stopConfig
        this.itemList = colItemList
        this.upList = upitem || []
        this.downList = downList || []
        let actions = stopConfig.actions || []
        for (let [i,action] of Object.entries(actions)){
            if (action["endSpeed"]) {
                this.actionQueue.push({["endSpeed"] : action["endSpeed"], ["totalTime"] : action["totalTime"], ["accelerationTime"] : action["accelerationTime"], ["beginFun"] : action["beginFun"], ["endFun"] : action["endFun"]});
            }
        }
        this.onDelayStopSchedule = true
        this.delayTotalTime = 0
    },

    onReelStartStop(){
        if (this.curStatus != ReelStatus.stop && this.curStatus != ReelStatus.downbounce) {
            this.clearActions()
            this.curStatus = ReelStatus.stopping
            this.isReelStoping = true
            if (this.stopConfig.stopType===1) {
                this.normalStop()
                this.parseNormalBounceDown()
            }
            else if (this.stopConfig.stopType===0) {
                this.paseEaseStop()
                this.parseNormalBounceDown()
            }
            else if (this.stopConfig.stopType===2) {
                this.pasePhyicalStop()
                this.parsePhysicalBounceDown()
            }
            else if (this.stopConfig.stopType===3) {
                this.normalStop()
                this.parseFastBounceDown()
            }
        }
    },

    clearActions(){
        this.actionQueue = []
        this.curAction = null
        this.onDelayStopSchedule = false
    },

    // 
    normalStop(){
        let extraCount = this.ExtraSymbolCount
        //todo
        if (this.gameCtrl.isTurbo) then
            extraCount = extraCount / 2
        end
        let aimPosition = this.getNextCell().y
        let distance = -(aimPosition + this.stopConfig.downBounce + (extraCount + 0.5) * this.cellHeight - (this.basePos.y + this.adjustPos.y))
        let endSpeed = distance / this.stopConfig.speedDownTime
        let onReelReachBottom = function(){
            this.gameCtrl.onReelFallBottom(this.colid)
            this.curStatus = ReelStatus.downbounce
            this.curSpeed = 0
        }.bind(this)
        this.actionQueue.push({["endSpeed"] : endSpeed, ["totalTime"] : this.stopConfig.speedDownTime, ["endFun"] : onReelReachBottom})
    },

    // 
    paseEaseStop(){
        let extraCount = this.ExtraSymbolCount
        //todo
        if (this.gameCtrl.isTurbo) {
            extraCount = extraCount / 2
        }
        let bounceDis = extraCount * this.cellHeight
        let distance = (this.getNextCell().y + this.stopConfig.downBounce + 0.5 * this.cellHeight - (this.basePos.y + this.adjustPos.y))
        let t1 = -bounceDis / this.spinConfig.maxSpeed
        let v2 = -distance / (this.stopConfig.speedDownTime - t1)
        let onReelReachBottom = function(){
            this.curSpeed = 0
            this.curStatus = ReelStatus.downbounce
            this.gameCtrl.onReelFallBottom(this.colid)
        }.bind(this)
        this.actionQueue.push({["endSpeed"] : this.spinConfig.maxSpeed, ["totalTime"] : t1});
        this.actionQueue.push({["endSpeed"] : v2, ["totalTime"] : this.stopConfig.speedDownTime - t1, ["endFun"] : onReelReachBottom});
    },

    // 
    pasePhyicalStop(){
        let extraCount = this.ExtraSymbolCount
        //todo
        if (this.gameCtrl.isTurbo) {
            extraCount = extraCount / 2
        }
        let bounceDis = extraCount * this.cellHeight
        let distance = (this.getNextCell().y + 0.5 * this.cellHeight - (this.basePos.y + this.adjustPos.y))
        let speed = -(bounceDis + distance) / this.stopConfig.speedDownTime
        let onReelReachBottom = function(){
            this.curSpeed = 0
            this.curStatus = ReelStatus.downbounce
            this.gameCtrl.onReelFallBottom(this.colid)
        }.bind(this)
        this.actionQueue.push({["endSpeed"] : speed, ["totalTime"] : this.stopConfig.speedDownTime, ["endFun"] : onReelReachBottom});  
    },

    // 
    parseNormalBounceDown(){
    
        let onReelStoped = function(){
            this.curStatus = ReelStatus.stop
            this.fixResult()
            this.isReelStoping = false
            this.gameCtrl.onReelStop(this.colid)
            this.spinLayer.checkDealWithReelStop()
        }.bind(this)
        this.curSpeed = 0
    
        let speed = this.stopConfig.downBounce / this.stopConfig.downBounceTime
        this.actionQueue.push({["endSpeed"] : speed, ["totalTime"] : this.stopConfig.downBounceTime});
        this.actionQueue.push({["delay"] : 1, ["totalTime"] : 0, ["beginFun"] : onReelStoped});
    },

    // 
    parseFastBounceDown(){
        let onReelStoped = function(){
            this.curStatus = ReelStatus.stop
            this.fixResult()
            this.isReelStoping = false
            this.gameCtrl.onReelStop(this.colid)
            this.spinLayer.checkDealWithReelStop()
        }.bind(this)
        this.curSpeed = 0
        let speed = this.stopConfig.downBounce / this.stopConfig.downBounceTime
        this.actionQueue.push({["endSpeed"] : speed, ["totalTime"] : this.stopConfig.downBounceTime});
        this.actionQueue.push({["delay"] : 1, ["totalTime"] : 0, ["beginFun"] : onReelStoped});
    },

    // 
    parsePhysicalBounceDown(){
        let onReelStoped = function(){
            this.curStatus = ReelStatus.stop
            this.fixResult()
            this.isReelStoping = false
            this.gameCtrl.onReelStop(this.colid)
            this.spinLayer.checkDealWithReelStop()
        }.bind(this)
        this.curSpeed = 0

        this.actionQueue.push({["endSpeed"] : this.stopConfig.shakeDistance, ["totalTime"] : this.stopConfig.downBounceTime})
        this.actionQueue.push({["endSpeed"] : -this.stopConfig.shakeDistance, ["totalTime"] : this.stopConfig.downBounceTime})
        this.actionQueue.push({["endSpeed"] : this.stopConfig.shakeDistance, ["totalTime"] : this.stopConfig.downBounceTime})
        this.actionQueue.push({["endSpeed"] : -this.stopConfig.shakeDistance, ["totalTime"] : this.stopConfig.downBounceTime})
        this.actionQueue.push({["endSpeed"] : this.stopConfig.shakeDistance, ["totalTime"] : this.stopConfig.downBounceTime})
        this.actionQueue.push({["endSpeed"] : -this.stopConfig.shakeDistance, ["totalTime"] : this.stopConfig.downBounceTime})
        this.actionQueue.push({["delay"] : 1, ["totalTime"] : 0, ["beginFun"] : onReelStoped})
    },   

    // 
    fixResult(){
        //todo
        let _currentCell = this.getCurrentCell()
        while (_currentCell.y < (this.basePos.y + this.adjustPos.y) + (this.row - 1) * this.cellHeight) {
            this.currentIndex = this.increaseIndex(this.currentIndex)
            _currentCell = this.getCurrentCell()
        }
        while (_currentCell.y > (this.basePos.y + this.adjustPos.y) + this.row * this.cellHeight) {
            this.currentIndex = this.decreaseIndex(this.currentIndex)
            _currentCell = this.getCurrentCell()
        }
    }, 

    // 
    fastStop(){
        if (this.curStatus===ReelStatus.stop || this.curStatus===ReelStatus.downbounce || ! this.itemList) {
            return null
        }
        this.curStatus = ReelStatus.downbounce
        this.clearActions()
        this.fastResetResult()
        if (this.stopConfig.stopType===1) {
            this.parseNormalBounceDown()
        }
        else if (this.stopConfig.stopType===0) {
            this.parseNormalBounceDown()
        }
        else if (this.stopConfig.stopType===2) {
            this.parsePhysicalBounceDown()
        }
        else if (this.stopConfig.stopType===3) {
            this.parseFastBounceDown()
        }
        
        //todo
        this.gameCtrl.onReelFastFallBottom(this.colid)
    },  
    
    fastResetResult(){
        this.curSpeed = 0

        let resultIndex = 1
        let moveY = this.getCurrentCell().y + this.cellHeight / 2 - (this.basePos.y + this.adjustPos.y) + this.stopConfig.downBounce

        if (this.downList.length > 0) {
            //todo
            this.gameCtrl.updateCellSprite(this.cells[this.currentIndex], this.colid, true, this.downList[1], true)
        }

        let tmp_i = this.currentIndex
        let tmp_length = this.currentIndex + this.row

        for (let i = tmp_i; i < tmp_length;i++) {
            this.currentIndex = this.increaseIndex(this.currentIndex)
            //todo
            this.gameCtrl.updateCellSprite(this.getCurrentCell(), this.colid, true, this.itemList[this.row - resultIndex], true)
            resultIndex = resultIndex + 1
        }

        let index = (this.currentIndex + 1) % (this.number)

        if (this.upList.length > 0) {
            //todo
            this.gameCtrl.updateCellSprite(this.cells[index], this.colid, true, this.upList[1], true)
        }
        for (let [k,theCell] of Object.entries(this.cells)){
            theCell.y = theCell.y - moveY
        }
    },    


});

module.exports = GameSpinReel;