
let FishGlobalDataProxy = require('FishGlobalDataProxy');
let GameLobbyLoginProxy = require('GameLobbyLoginProxy');
let GameListDataProxy = require('GameListDataProxy');

module.exports = puremvc.define({
	parent : puremvc.SimpleCommand
},
{
	execute : function() {
		this.facade.addProxy(new FishGlobalDataProxy());
		this.facade.addProxy(new GameLobbyLoginProxy());

		this.facade.addProxy(new GameListDataProxy());
		

		this.facade.registerProxy();
	}
},
{
	NAME : 'FishPrepModelCommand'
});