datatypes.CMD_GP_LogonByAccounts = {
	'mainCmdID': constVal.MDM_GP_LOGON,
	'subCmdID': constVal.SUB_GP_LOGON_ACCOUNTS,
	'argTypes': [
		{
			'name': 'dwPlazaVersion',
		},
		{
			'name': 'dwPlazaResVersion',
		},
		{
			'name': 'szAccounts',
		},
		{
			'name': 'szPassword',
		},
		{
			'name': 'szChannel',
		},
		{
			'name': 'cbDeviceType',
		},
		{
			'name': 'szMachineID',
		},
		{
			'name': 'szSystemVersion',
		},
		{
			'name': 'szSystemLanguage',
		},
		{
			'name': 'szSystemModel',
		},
		{
			'name': 'szSystemBrand',
		},
	],

	write2bundle: function (bundle, args) {
		bundle.writeUint32(args[0]);
		bundle.writeUint32(args[1]);
		bundle.writeString(args[2], 2*constVal.NAME_LEN);
		bundle.writeString(args[3], 2*constVal.PASS_LEN);
		bundle.writeString(args[4], 2*constVal.NAME_LEN);
		bundle.writeUint8(args[5]);
		bundle.writeString(args[6], 2*64);
		bundle.writeString(args[7], 2*20);
		bundle.writeString(args[8], 2*20);
		bundle.writeString(args[9], 2*64);
		bundle.writeString(args[10], 2*64);
	},

	size: function () {
		return 1 + 8 + (constVal.NAME_LEN + constVal.NAME_LEN + constVal.PASS_LEN + 64 * 3 + 20 * 2) * 2;
	},

};

