datatypes.CMD_GP_LogonSuccess = {
	'mainCmdID': constVal.MDM_GP_LOGON,
	'subCmdID': constVal.SUB_GP_LOGON_SUCCESS,
	'argTypes': [
		{
			'name': 'wFaceID',
		},
		{
			'name': 'cbGender',
		},
		{
			'name': 'dwUserID',
		},
		{
			'name': 'dwGameID',
		},
		{
			'name': 'szAccount',
		},
		{
			'name': 'szNickName',
		},
		{
			'name': 'szToken',
		},
		{
			'name': 'szUnderWrite',
		},
		{
			'name': 'szSession',
		},
		{
			'name': 'lUserScore',
		},
		{
			'name': 'lDiamond',
		},
		{
			'name': 'lLpValues',
		},
		{
			'name': 'lTicket',
		},
		{
			'name': 'dwLoginType',
		},
		{
			'name': 'dwVipLv',
		},
		{
			'name': 'dwVipPoint',
		},
		{
			'name': 'dwClubPoint',
		},
		{
			'name': 'lExperience',
		},
		{
			'name': 'dwCurExLevel',
		},
		{
			'name': 'dwNextExPoint',
		},
		{
			'name': 'lNextExScore',
		},
		{
			'name': 'szExtend',
		},
	],
	createFromStream: function (stream, args) {
		
		let wFaceID = stream.readUint16(); 		//
		let cbGender = stream.readUint8(); 		//
		let dwUserID = stream.readUint32(); 	// I D
		let dwGameID = stream.readUint32(); 	// I D
		let szAccount = stream.readStringUTF8(constVal.NAME_LEN * 2); 	//
		let szNickName = stream.readStringUTF8(constVal.NAME_LEN * 2); 	//
		let szToken = stream.readStringUTF8(constVal.PASS_LEN * 2); 		//
		let szUnderWrite = stream.readStringUTF8(32 * 2);
		let szSession = stream.readStringUTF8(constVal.PASS_LEN * 2);

		//
		let lUserScore = stream.readInt64().lo;  	//
		let lDiamond = stream.readInt64().lo;		//
		let lLpValues = stream.readInt64().lo; 	
		let lTicket = stream.readInt64().lo;

		let dwLoginType = stream.readUint32();

		//
		let dwVipLv = stream.readUint32();
		let dwVipPoint = stream.readUint32();
		let dwClubPoint = stream.readUint32();

		//
		let lExperience = stream.readInt64().lo;	//
		let dwCurExLevel = stream.readUint32();	//
		let dwNextExPoint = stream.readInt64().lo;//
		let lNextExScore = stream.readUint32();
		let szExtend = stream.readStringUTF8(128 * 2);
		

		args.push(wFaceID, cbGender, dwUserID, dwGameID, szAccount, szNickName, szToken, szUnderWrite, szSession, lUserScore, lDiamond, lLpValues, lTicket, dwLoginType, dwVipLv, dwVipPoint, dwClubPoint,lExperience,dwCurExLevel,dwNextExPoint,lNextExScore,szExtend);
	}
};

