//Lua
datatypes.tagLuaGameKind = {
	'mainCmdID': constVal.MDM_GP_SERVER_LIST,
	'subCmdID': constVal.SUB_GP_LIST_LUA_GAME,
	'argTypes': [
		{
			'name': 'wKindID',
			'type': 'UINT16'
		},
		{
			'name': 'szKindName',
			'type': 'STRING',
			'len': constVal.KIND_LEN
		},
		{
			'name': 'wTypeID',
			'type': 'UINT16'
		},
		{
			'name': 'wSortID',
			'type': 'UINT16'
		},
		{
			'name': 'dwClientVersion',
			'type': 'UINT32'
		},
		{
			'name': 'dwResVersion',
			'type': 'UINT32'
		},
		{
			'name': 'cbNullity',
			'type': 'UINT8'
		},
		{
			'name': 'szExtend',
			'type': 'STRING',
			'len': 256
		},
	],

	createFromStream: function (stream, args) {
		let gameItmeList = [];
		for (let i = 0; i < (stream.buffer.byteLength - 8) / 591; i++) {
			item = {};
			item.wKindID = stream.readUint16(); //ID						
			item.szKindName = stream.readStringUTF8(constVal.KIND_LEN * 2); //
			item.wTypeID = stream.readUint16(); //
			item.wSortID = stream.readUint16(); //
			item.dwClientVersion = stream.readUint32(); //
			item.dwResVersion = stream.readUint32(); //
			item.cbNullity = stream.readUint8(); // 0  1 
			item.szExtend = stream.readStringUTF8(256 * 2); //json

			
			if(item.wKindID == 520){

				gameItmeList.push(item)	

			}
					
		}


		args.push(gameItmeList);
	}
};



