datatypes.CMD_GP_H5RegisterAccounts = {
	'mainCmdID': constVal.MDM_GP_LOGON,
	'subCmdID': constVal.SUB_GP_H5_REGISTER_ACCOUNTS,
	'argTypes': [
		{
			'name': 'lChannelID',
		},
		{
			'name': 'szHeadImgUrl',
			'type': 'STRING',
			'len': constVal.URL_LEN
		},
		{
			'name': 'dwOpenID',
		},
		{
			'name': 'dwUnionID',
		},
		{
			'name': 'szAccounts',
			'type': 'STRING',
			'len': constVal.NAME_LEN
		},
		{
			'name': 'szPassWord',
			'type': 'STRING',
			'len': constVal.PASS_LEN
		},
		{
			'name': 'cbSex',
			'type': 'UINT8'
		},
		{
			'name': 'linviteUserID',
		},
		{
			'name': 'cbsystem',
			'type': 'UINT8'
		},
		{
			'name': 'szNickname',
			'type': 'STRING',
			'len': constVal.NAME_LEN
		}
	],
	write2bundle: function (bundle, args) {
		bundle.writeUint32(args[0])
		bundle.writeString(args[1], constVal.URL_LEN);			//url
		bundle.writeString(args[2], 48);						//id
		bundle.writeString(args[3], 48);						//id
		bundle.writeString(args[4], constVal.NAME_LEN);			//
		bundle.writeString(args[5], constVal.PASS_LEN);	        //
		bundle.writeUint8(args[6]);			                    //
		bundle.writeUint32(args[7])
		bundle.writeUint32(args[8]);			                    //01android2ios
		bundle.writeString(args[9], constVal.NAME_LEN);			//
	},
	size: function () {
		return 12 + constVal.URL_LEN + 48 + 48 + constVal.NAME_LEN + constVal.PASS_LEN + 1 + constVal.NAME_LEN;
	}
};


