//
let PerlinNoise = function () {
	this.reset()
};
PerlinNoise.prototype = {
	constructor: PerlinNoise,
	reset() {
	},
	noise1d(x) {
		x = x << 13 ^ x;
		let t = (Math.imul(x, (Math.imul(Math.imul(x, x), 15731) + 789221)) + 1376312589) & 0x7fffffff;
		return 1.0 - t * 0.931322574615478515625e-9;/// 1073741824.0);
	},
	generateNoise1D(x) {
		let ix = Math.floor(x);
		let s = x - ix;
		let r1 = this.noise1d(ix);
		let r2 = this.noise1d(ix + 1);
		return this.interpolate(r1, r2, s)
	},
	interpolate(x, y, s) {
		//cc.log("x, y, s", x, y, s)
		let ft = s * 3.1415927;
		let f = (1.0 - Math.cos(ft)) * 0.5;
		//cc.log("x * (1.0 - f) + y * f", x * (1.0 - f) + y * f)
		return x * (1.0 - f) + y * f;
	},
}
module.exports = new PerlinNoise();

