


module.exports = puremvc.define(
	{
		parent: puremvc.SimpleCommand
	},
	{
		execute: function (gameinfo,app) {

			let _gameinfo = gameinfo

			//
			let gameProxy = this.facade.retrieveProxy('GameProxy');
			if(gameProxy != null){	
				this.facade.removeProxy('GameProxy');
			}

			let GameProxy = require('Game_'+ _gameinfo +'_Proxy');
			this.facade.addProxy(new GameProxy());	

			gameProxy = this.facade.retrieveProxy('GameProxy');


			//
			let gameTableProxy = this.facade.retrieveProxy('GameTableProxy');
			if(gameTableProxy != null){	
				this.facade.removeProxy('GameTableProxy');
			}
			let GameTableProxy = require('GameTableProxy');
			this.facade.addProxy(new GameTableProxy());

			gameTableProxy = this.facade.retrieveProxy('GameTableProxy');
			this.facade.registerProxy();
			

			// let app = KBEngine.createApp();
			// app.wssAddr = LobbyServerAddr;


			gameTableProxy.registerNetInterface(app,_gameinfo);
			gameProxy.registerNetInterface(app,_gameinfo);


		}
	},
	{
		NAME: 'EnterGameScenceCommand'
	});