let GameSpinReel = require('GameSpinReel');


let ReelStatus = {
    ["start"]           : 1,
    ["running"]         : 2,
    ["stopping"]        : 3,
    ["downbounce"]      : 4,
    ["stop"]            : 5
};

let GameSpinLayer = cc.Class({
    extends: cc.Component,

    properties: {
        winSize:cc.Size,
    },

    onLoad (reelConfig) {
        let winSize = cc.winSize
        this.node.setAnchorPoint(0.5, 0.5)
        this.node.setContentSize(winSize)
        this.node.setPosition(cc.v2(winSize.width / 2, winSize.height / 2))
    },

    init(){
        let reelConfigList = arguments[0]
        let boardNode = arguments[1]
        let gameViewLayer = arguments[2]

        this.isActive = false
        this.running = false
        this.stopping = false
    
        this.boardNode = boardNode
        this.reelConfig = reelConfigList
        this.spinReels = []
        for (let [col,reelConfig] of Object.entries(reelConfigList)){
            let reelNode = boardNode.getReelNode(col)
            let spinReel = new GameSpinReel(col, this, reelNode, reelConfig,gameViewLayer)
            reelNode.addChild(spinReel)
            this.spinReels.push(spinReel)
        }
    },

    Active()
    {
        this.isActive = true;
        this.boardNode.active = true;
    
        let i = 0
        let total = 0
        let dt_60 = []


        this.onUpdate = function(dt){
            if (dt_60[i]) {
                total = total - dt_60[i]
            }

            dt_60[i] = dt
            total = total + dt
            i = i + 1
            if (i > 60) {
                i = 1
            }

            let calc_dt = total / dt_60.length
            if (calc_dt > 1 / 30) {
                dt_60 = []
                i = 1
            }
            else{
                dt = calc_dt
            }
            if (dt===null ){
                cc.log(dt)
            }

            if (this.isActive && this.spinReels != null) {
                for (let [col,spinReel] of Object.entries(this.spinReels)){
                    
                    spinReel.updateCallFunc(dt)
                }
            }
        
        }   
        this.timer = cc.director.getScheduler()
        this.timer.schedule(this.onUpdate, this, 0, false);
        
    },

    DeActive(){
        this.isActive = false
        this.running = false
        this.stopping = false
        this.boardNode.active = false
        this.resetBoard()
        if(this.timer){
            this.timer.unschedule(this.onUpdate,this);
        }
    },

    resetBoard(){
        for (let [col,_] of Object.entries(this.spinReels)){
            this.spinReels[col].resetCol();
        }
    },

    setCellStatus(visible){
        for (let [index,theReel] of Object.entries(this.spinReels)){
            theReel.setCellStatus(visible)
        }
    },

    getCell(col, row){
        return this.spinReels[col].getRetCell(Number(row) + 1)
    },

    getCellSize(colId){
        return new cc.Size(this.reelConfig[colId].cellWidth, this.reelConfig[colId].cellHeight)
    },

    getCellPos(col, row){
        let reel = this.spinReels[col]
        let addPos = cc.v2(0.5 * reel.cellWidth, (reel.row - row + 0.5) * reel.cellHeight)
        return (this.spinReels[col].basePos.add(addPos)).add(this.spinReels[col].adjustPos)
    },

    checkDealWithReelStop(){
        let hasSpinningReel = false
        for (let [index,theReel] of Object.entries(this.spinReels)){
            if (theReel.curStatus != ReelStatus.stop) {
                hasSpinningReel = true
                break
            }
        }
        
        if (!hasSpinningReel) {
            this.running = false
            this.stopping = false
            if (this.endCallFunc) {
                this.endCallFunc()
                this.endCallFunc = null
            }
        }
    },

    spin(spinConfig){
        if (this.running) {
            return null
        }
        this.running = true
        this.endCallFunc = null
        this.itemList = null
        for (let [_,theReelConfig] of Object.entries(spinConfig)){
            let col = theReelConfig["col"]
            let actionConfig = theReelConfig["action"]
            this.spinReels[col].spin(actionConfig)
        }
    },

    stop(stopConfig, stopItemList, uplist, downlist, endCallFunc){
        if (!this.running || this.stopping) {
            return null
        }
        this.stopping = true
        this.endCallFunc = endCallFunc
        this.itemList = stopItemList
        for (let [index,stopReelConfig] of Object.entries(stopConfig)){
            let theCol = stopReelConfig["col"]
            let theAction = stopReelConfig["action"]
            let colItemList = stopItemList[theCol]
            this.spinReels[theCol].stop(theAction, colItemList, uplist && uplist[theCol], downlist && downlist[theCol])
        }
    },

    fastStop(){
        if (!this.running || !this.itemList) {
            return null
        }
        for (let [_,reel] of Object.entries(this.spinReels)){
            reel.fastStop()
        }
    },

});
module.exports = GameSpinLayer;