datatypes.CMD_S_StatusFree = {

	createFromStream: function (stream) {

        let tmpMap = {};

        //
        tmpMap.lCellScore = stream.readInt64().lo;   // 
        
        tmpMap.cbBetIndex = stream.readUint8();   // 

        tmpMap.cbBetCount = stream.readUint8();   // 

        tmpMap.lBetScoreArr = [];

        for(let i = 0; i < constVal.MAX_BET_COUNT ; i++){
            let lBetScore = stream.readInt64().lo;   // 
            tmpMap.lBetScoreArr.push(lBetScore);
        };


        tmpMap.lLotteryScore = stream.readInt64().lo;   // 

        tmpMap.bUnlockRewardID = stream.readUint8();   // id1-431,23

        tmpMap.RewardPoolArr = [];

        for (let i = 0; i < constVal.MAX_REWARD_POOL_INDEX ; i++) {
            let llRewardPool = stream.readInt64().lo;
            tmpMap.RewardPoolArr.push(llRewardPool);
        };


		return tmpMap;
	}
};

//(PLAY)
datatypes.CMD_S_StatusPlay = {

	createFromStream: function (stream) {

        let tmpMap = {};

        //
        tmpMap.lCellScore = stream.readInt64().lo;   // 
    
        tmpMap.cbBetCount = stream.readUint8();   // 

        tmpMap.lBetScoreArr = [];

        for(let i = 0; i < constVal.MAX_BET_COUNT ; i++){
            let lBetScore = stream.readInt64().lo;   // 
            tmpMap.lBetScoreArr.push(lBetScore);
        };

		tmpMap.cbLineCount = stream.readUint8();   // 
		tmpMap.cbBetIndex = stream.readUint8();   // 

		tmpMap.lLineScore = [];
        for(let i = 0; i < constVal.MAX_LINE_COUNT ; i++){
            tmpMap.lLineScore.push(stream.readInt64().lo);// 
        };

		tmpMap.cbItemType = [];
        for(let i = 0; i < constVal.MAX_LINE_COUNT ; i++){
            tmpMap.cbItemType.push(stream.readUint8());// 
        };

		tmpMap.cbItemLineCount = [];
        for(let i = 0; i < constVal.MAX_LINE_COUNT ; i++){
            tmpMap.cbItemLineCount.push(stream.readUint8());// 
        };

		tmpMap.ItemInfo = [];
        for(let i = 0; i < 3 ; i++){
			tmpMap.ItemInfo[i] = []
			for(let j = 0; j < constVal.ITEM_X_COUNT ; j++){
				tmpMap.ItemInfo[i].push(stream.readUint8());
			}
        };

		tmpMap.nGiveAwayCount = stream.readUint32();//
		tmpMap.nTotalGiveAwayCount = stream.readUint32();
		tmpMap.cbFreeSpinState = stream.readUint8();
		tmpMap.dUserTotalWinScore = stream.readInt64().lo
		tmpMap.lTotalGameScore = stream.readInt64().lo//
		tmpMap.lLotteryScore = stream.readInt64().lo//
		tmpMap.bCanBn = stream.readUint8();       	//BN

		tmpMap.bVerifyMailBox = stream.readUint8();     //
		tmpMap.bUnlockRewardID = stream.readUint8();    //id
		tmpMap.bBnSenceId = stream.readUint8();       	//id0-3
		tmpMap.bRewardGoodsNum = stream.readUint8();    //
		
		tmpMap.bRewardGoodChoose = [];
		for(let i = 0; i < 20 ; i++){
			tmpMap.bRewardGoodChoose.push(stream.readUint8());//id
		}

		tmpMap.llRewardNum = stream.readInt64().lo		//
		tmpMap.bSkullNum = stream.readUint8();    		//

		return tmpMap;
	}
};

