datatypes.CMD_S_GameStart = {
	'mainCmdID': constVal.MDM_GF_GAME,
	'subCmdID': constVal.SUB_S_GAME_START,
	'argTypes': [
		{
			'name': 'ItemInfo',
			'type': 'INT8'
		},
        {
			'name': 'WildStickInfo',
			'type': 'INT8'
		},
        {
			'name': 'dLineScore',
			'type': 'INT64'
		},
        {
			'name': 'cbItemType',
			'type': 'INT8'
		},
        {
			'name': 'cbLineCount',
			'type': 'INT8'
		},
        {
			'name': 'nGiveAwayCount',
			'type': 'INT16'
		},
        {
			'name': 'nTotalGiveAwayCount',
			'type': 'INT16'
		},
        {
			'name': 'cbFreeSpinState',
			'type': 'INT8'
		},
        {
			'name': 'dUserTotalWinScore',
			'type': 'INT64'
		},
        {
			'name': 'bCanBn',
			'type': 'INT8'
		},
	],

	createFromStream: function (stream, args) {

        let data = {}

        data.ItemInfo = [[],[],[]];

        for (let i = 0; i < 3; i++) {
            for (let j = 0; j < constVal.ITEM_X_COUNT; j++) {
                data.ItemInfo[i][j] = stream.readUint8();
            };
        };

        data.WildStickInfo = [[],[],[]];

        for (let i = 0; i < 3; i++) {
            for (let j = 0; j < constVal.ITEM_X_COUNT; j++) {
                data.WildStickInfo[i][j] = stream.readUint8();
            };
        };

        data.dLineScore = []

        for (let i = 0; i < constVal.MAX_LINE_COUNT; i++) {
            data.dLineScore[i] = stream.readInt64().lo;
        };

        data.cbItemType = []

        for (let i = 0; i < constVal.MAX_LINE_COUNT; i++) {
            data.cbItemType[i] = stream.readUint8();
        };

        data.cbLineCount = []

        for (let i = 0; i < constVal.MAX_LINE_COUNT; i++) {
            data.cbLineCount[i] = stream.readUint8();
        };

        data.nGiveAwayCount = stream.readUint32();
        data.nTotalGiveAwayCount = stream.readUint32();
        data.cbFreeSpinState = stream.readUint8();
        data.dUserTotalWinScore = stream.readInt64().lo;
        data.bCanBn = stream.readUint8();             

		args.push(data);
	}
};

