datatypes.CMD_S_RewardChoose = {
	'mainCmdID': constVal.MDM_GF_GAME,
	'subCmdID': constVal.Sub_S_REWARD_CHOOSE,
	'argTypes': [
		{
			'name': 'bRewardGoodsNum',
			'type': 'INT8'
		},
		{
			'name': 'llRewardValue',
			'type': 'INT64'
		},
		{
			'name': 'cbGoodsID',
			'type': 'INT8'
		},
		{
			'name': 'stSendMainbox',
			'type': 'INT64'
		},
	],

	createFromStream: function (stream, args) {

    	let data = {}
		data.bRewardGoodsID = stream.readUint8();			//(1-3)
		data.llRewardValue = stream.readInt64().lo;	//
		data.cbGoodsID = stream.readUint8()					//id

		data.stSendMainbox = [];

        for (let i = 0; i < constVal.MAX_REWARD_POOL_INDEX ; i++) {
            let llRewardPool = stream.readInt64().lo;
            data.stSendMainbox.push(llRewardPool);
        };

		args.push(data);

	}
};

