let m_PerlinNoise = require('Noise');
let gfx = cc.gfx;

cc.Class({
    extends: cc.Component,
    // editor: {
    //     executeInEditMode: true
    // },

    properties: {
        //speed: 50
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},

    // start2() {
    //     var vfmt = new gfx.VertexFormat([
    //         { name: gfx.ATTR_POSITION, type: gfx.ATTR_TYPE_FLOAT32, num: 2 },
    //         { name: gfx.ATTR_UV0, type: gfx.ATTR_TYPE_FLOAT32, num: 2 },
    //         { name: gfx.ATTR_COLOR, type: gfx.ATTR_TYPE_UINT8, num: 4, normalize: true },
    //     ]);

    //     let mesh = new cc.Mesh();
    //     mesh.init(vfmt, 9, true);
    //     this.mesh = mesh;

    //     this.vertexes = [
    //         cc.v2(-100, 100), cc.v2(100, 100),
    //         cc.v2(100, -100), cc.v2(-100, -100)
    //     ];
    //     cc.log(JSON.stringify(this.vertexes))
    //     mesh.setVertices(gfx.ATTR_POSITION, this.vertexes);

    //     mesh.setVertices(gfx.ATTR_UV0, [
    //         cc.v2(0, 0), cc.v2(1, 0),
    //         cc.v2(1, 1), cc.v2(0, 1),
    //     ]);

    //     this.mesh.setVertices(gfx.ATTR_COLOR, [
    //         cc.color(255, 0, 0, 255), cc.color(255, 0, 0, 255), cc.color(255, 0, 0, 255), cc.color(255, 0, 0, 255),
    //     ]);

    //     mesh.setIndices([
    //         0, 1, 2, 0, 2, 3,
    //     ]);

    //     let renderer = this.node.getComponent(cc.MeshRenderer);
    //     if (!renderer) {
    //         renderer = this.node.addComponent(cc.MeshRenderer);
    //     }
    //     renderer.mesh = mesh;
    //     this.mesh = mesh;
    // },

    start() {

    },
    setlightLine(Lines) {
        //cc.log("setlightLine", Lines)
        var vfmt = new gfx.VertexFormat([
            { name: gfx.ATTR_POSITION, type: gfx.ATTR_TYPE_FLOAT32, num: 2 },
            { name: gfx.ATTR_UV0, type: gfx.ATTR_TYPE_FLOAT32, num: 2 },
            { name: gfx.ATTR_COLOR, type: gfx.ATTR_TYPE_UINT8, num: 4, normalize: true },
        ]);

        let mesh = new cc.Mesh();
        mesh.init(vfmt, 9, true);
        this.mesh = mesh;

        this.m_Time = 0
        this.m_Width = 80
        this.m_Step = 10
        this.m_TextureLength = 100
        this.m_Amplitude0 = 10
        this.m_Frequency0 = 500
        this.m_ProgramState = null
        this.m_TimeFactor = 0.5
        this.m_fTotalAlphaToZeroTime = 1.0
        this.m_fLeaveAlphaToZeroTime = 1.0
        this.dirty = true;
        this.initialCapacity = 0;
        this.m_Vertices = null;
        this.tTriangle = null;
        this.vertexBuffer = null;
        this.indexBuffer = null;
        this.m_Lines = Lines
        
        if (Lines.length > 5) {
            this.m_Step = 10 + (Lines.length - 5) * 2
        }
        // let Line = []
        // Line.lineBegin = cc.v3(-100, -100, 0)
        // Line.lineEnd = cc.v3(100, 100, 0)
        // this.m_Lines.push(Line)
        this.updateMesh()

        let renderer = this.node.getComponent(cc.MeshRenderer);
        renderer.mesh = mesh;
        //  this.mesh = mesh;
    },
    update(dt) {
        //  if (CC_EDITOR) return;
        if (this.mesh) {
            this.updateMesh()
            this.m_Time += dt;
            if (this.m_Time > 2.5) {
                this.m_Time = 0
            }
        }
    },
    addOneStep(lineBegin, lineEnd, widthOffset, uvBegin, uvEnd, stepType) {

        // this.vertexesCOLOR[this.m_VertexSize] = cc.Color.RED
        // this.vertexesCOLOR[this.m_VertexSize + 1] = cc.Color.RED
        // this.vertexesCOLOR[this.m_VertexSize + 2] = cc.Color.RED
        // this.vertexesCOLOR[this.m_VertexSize + 3] = cc.Color.RED

        switch (stepType) {
            case 1:
                {
                    this.m_Vertices[this.m_VertexSize] = cc.v2(lineBegin.x, lineBegin.y)
                    this.vertexesUV0[this.m_VertexSize] = cc.v2(uvBegin, 0.5)
                    this.m_VertexSize += 1;

                    this.m_Vertices[this.m_VertexSize] = cc.v2(lineEnd.x + widthOffset.x, lineEnd.y + widthOffset.y)
                    this.vertexesUV0[this.m_VertexSize] = cc.v2(uvEnd, 0)
                    this.m_VertexSize += 1;

                    this.m_Vertices[this.m_VertexSize] = cc.v2(lineEnd.x - widthOffset.x, lineEnd.y - widthOffset.y)
                    this.vertexesUV0[this.m_VertexSize] = cc.v2(uvEnd, 1)
                    this.m_VertexSize += 1;

                    this.m_Vertices[this.m_VertexSize] = cc.v2(lineBegin.x, lineBegin.y)
                    this.vertexesUV0[this.m_VertexSize] = cc.v2(uvBegin, 0.5)
                    this.m_VertexSize += 1;

                } break;
            case 3:
                {
                    this.m_Vertices[this.m_VertexSize] = cc.v2(lineBegin.x + widthOffset.x, lineBegin.y + widthOffset.y)
                    this.vertexesUV0[this.m_VertexSize] = cc.v2(uvBegin, 0)
                    this.m_VertexSize += 1;

                    this.m_Vertices[this.m_VertexSize] = cc.v2(lineEnd.x, lineEnd.y)
                    this.vertexesUV0[this.m_VertexSize] = cc.v2(uvEnd, 0.5)
                    this.m_VertexSize += 1;

                    this.m_Vertices[this.m_VertexSize] = cc.v2(lineEnd.x, lineEnd.y)
                    this.vertexesUV0[this.m_VertexSize] = cc.v2(uvEnd, 0.5)
                    this.m_VertexSize += 1;

                    this.m_Vertices[this.m_VertexSize] = cc.v2(lineBegin.x - widthOffset.x, lineBegin.y - widthOffset.y)
                    this.vertexesUV0[this.m_VertexSize] = cc.v2(uvBegin, 1)
                    this.m_VertexSize += 1;

                } break;
            case 2:
                {
                    this.m_Vertices[this.m_VertexSize] = cc.v2(lineBegin.x + widthOffset.x, lineBegin.y + widthOffset.y)
                    this.vertexesUV0[this.m_VertexSize] = cc.v2(uvBegin, 0)
                    this.m_VertexSize += 1;

                    this.m_Vertices[this.m_VertexSize] = cc.v2(lineEnd.x + widthOffset.x, lineEnd.y + widthOffset.y)
                    this.vertexesUV0[this.m_VertexSize] = cc.v2(uvEnd, 0)
                    this.m_VertexSize += 1;

                    this.m_Vertices[this.m_VertexSize] = cc.v2(lineEnd.x - widthOffset.x, lineEnd.y - widthOffset.y)
                    this.vertexesUV0[this.m_VertexSize] = cc.v2(uvEnd, 1)
                    this.m_VertexSize += 1;

                    this.m_Vertices[this.m_VertexSize] = cc.v2(lineBegin.x - widthOffset.x, lineBegin.y - widthOffset.y)
                    this.vertexesUV0[this.m_VertexSize] = cc.v2(uvBegin, 1)
                    this.m_VertexSize += 1;
                } break;
        }
    },
    updateMesh() {
        if (this.dirty == true) {
            this.dirty = false;
            this.initialCapacity = 0;
            for (let i = 0; i < this.m_Lines.length; ++i) {
                let dir = this.m_Lines[i].lineEnd.sub(this.m_Lines[i].lineBegin)
                let totalLength = dir.mag();
                let stepCount = (totalLength <= this.m_Step * 2) ? 2 : Math.ceil(totalLength / this.m_Step);
                this.initialCapacity += stepCount;
            }
            //cc.log("initialCapacity", this.initialCapacity)
        }
        this.m_VertexSize = 0;
        this.m_Vertices = []
        this.vertexesUV0 = []
        //this.vertexesCOLOR = []

        // 
        for (let i = 0; i < this.m_Lines.length; ++i) {
            let halfWidth = this.m_Width * 0.5;
            let inverseTextueLength = (this.m_TextureLength != 0) ? (1 / this.m_TextureLength) : 1.0;

            let lineBegin = this.m_Lines[i].lineBegin
            let lineEnd = this.m_Lines[i].lineEnd
            let dir = lineEnd.sub(lineBegin)
            let totalLength = dir.mag();
            dir.normalizeSelf();
            let invTotalLength = (totalLength != 0) ? (1 / totalLength) : 1.0;

            let stepCount = (totalLength <= this.m_Step * 2) ? 2 : Math.ceil(totalLength / this.m_Step);
            let step = totalLength / stepCount;

            // 
            let widthOffset;
            let camForward = cc.v3(0, 0, 1);
            //cc.log("step", step)
            widthOffset = dir;
            widthOffset = widthOffset.cross(camForward);
            //cc.log("dir", JSON.stringify(dir))
            //cc.log("widthOffset", JSON.stringify(widthOffset))
            let stepBegin = lineBegin
            let stepEnd = lineEnd
            let curLength = 0.0

            for (let j = 0; j < stepCount - 1; ++j) {
                let noiseParam = (this.m_Time * this.m_TimeFactor + curLength * invTotalLength);
                let noise = m_PerlinNoise.generateNoise1D(noiseParam * this.m_Frequency0) * this.m_Amplitude0;
                //cc.log("noise", noise)
                let randomPos = cc.v2(widthOffset.x * noise, widthOffset.y * noise)
                stepEnd = (lineBegin.add(cc.v2(dir.x * (curLength + step), dir.y * (curLength + step)))).add(randomPos)
                //cc.log("stepEnd", JSON.stringify(stepEnd))
                this.addOneStep((j == 0) ? lineBegin : stepBegin, stepEnd,
                    cc.v2(widthOffset.x * halfWidth, widthOffset.y * halfWidth),
                    (curLength * inverseTextueLength - this.m_Time + 100) % 1, ((curLength + step) * inverseTextueLength - this.m_Time + 100) % 1,
                    (j == 0) ? 1 : 2);

                curLength += step;
                stepBegin = stepEnd;
            }
            this.addOneStep(stepBegin, lineEnd,
                cc.v2(widthOffset.x * halfWidth, widthOffset.y * halfWidth),
                (curLength * inverseTextueLength - this.m_Time + 100) % 1, (totalLength * inverseTextueLength - this.m_Time + 100) % 1, 3);
        }

        //cc.log(JSON.stringify(this.m_Vertices))
        //cc.log(JSON.stringify(this.vertexesUV0))
        this.mesh.setVertices(gfx.ATTR_POSITION, this.m_Vertices);
        this.mesh.setVertices(gfx.ATTR_UV0, this.vertexesUV0);
        // this.mesh.setVertices(gfx.ATTR_COLOR, this.vertexesCOLOR);

        let tTriangle = []
        for (let i = 0; i < this.initialCapacity; i++) {
            tTriangle[i * 6 + 0] = 0 + i * 4;
            tTriangle[i * 6 + 1] = 1 + i * 4;
            tTriangle[i * 6 + 2] = 2 + i * 4;
            tTriangle[i * 6 + 3] = 0 + i * 4;
            tTriangle[i * 6 + 4] = 2 + i * 4;
            tTriangle[i * 6 + 5] = 3 + i * 4;
        }
        //cc.log(JSON.stringify(tTriangle))
        this.mesh.setIndices(tTriangle);
    }
});
