let appFacade = require('FishFacade');
var UIManager = require('gameUIManager');
cc.Class({
    extends: cc.Component,

    properties: {
        txtContent: { default: null, type: cc.Label },
    },
    init(data) {
        this.txtContent.string = data[0]
        this.func = data[1]
    },
    start() {

    },
    onbtnOk() {
        if (this.func) {
            this.func()
        }
        this.closeSelf()
    },
    onBack() {
        this.closeSelf()
    },
    closeSelf() {
        if (this.node.kindId){
            let uiManager = new UIManager(this.node.kindId)
            uiManager.closeUI(this.node.viewId)
        }else{
            cc.log("the kindId is null");
        }
        
    }
});
