constVal.CMD_FRIST_MDM_LOBBY = 4000;

constVal.MDM_GL_LOBBY = constVal.CMD_FRIST_MDM_LOBBY + 1; //
constVal.SUB_GL_LOBBY_MSG = 4;

let datatypes = {};
//
datatypes.tagLobbyMessage = {
	'mainCmdID': constVal.MDM_GL_LOBBY,
	'subCmdID': constVal.SUB_GL_LOBBY_MSG,
	'argTypes': [
		{
			'name': 'wMessageType',
		},
		{
			'name': 'wMessageLength',
		},
		{
			'name': 'szContent',
		}
	],
	size: function () {
		return 4 + 200;
	},
	write2bundle: function (bundle, args) {
		bundle.writeUint16(1);
		bundle.writeUint16(200);
		bundle.writeString(args[0], 200);
	},
	createFromStream: function (stream, args) {
		let wMessageType = stream.readUint16();
		let wMessageLength = stream.readUint16();
		let szContent = stream.readStringUTF8(wMessageLength);
		args.push(wMessageType, wMessageLength, szContent);
	}
};

//
constVal.MDM_KN_COMMAND = 0; //
constVal.SUB_KN_DETECT_SOCKET = 1; //
constVal.SUB_KN_DETECT_ONLINE = 2; //
//
datatypes.CMD_KN_DetectSocket = {
	'mainCmdID': constVal.MDM_KN_COMMAND,
	'subCmdID': constVal.SUB_KN_DETECT_SOCKET,
	size: function () {
		return 8;
	},
	write2bundle: function (bundle, args) {
		bundle.writeUint32(args[0]);
		bundle.writeUint32(args[1]);
	},

};

module.exports = {
	'constVal': constVal,
	'datatypes': datatypes
};