let GameLobbyLoginCommand = require('GameLobbyLoginCommand');
;
let EnterGameScenceCommand = require('EnterGameScenceCommand');
let GameTableCommand = require('GameTableCommand');
let GameCommand = require('GameCommand');


module.exports = puremvc.define(
{
	parent : puremvc.SimpleCommand
},
{
	execute  : function() {
		this.facade.registerCommand(FishConstants.LOGIN_LOBBY, GameLobbyLoginCommand);

		this.facade.registerCommand(FishConstants.ENTER_GAME_SCENCE, EnterGameScenceCommand);
		this.facade.registerCommand(FishConstants.GAME_TABLE_COMMAND, GameTableCommand);	//
		this.facade.registerCommand(FishConstants.GAME_COMMAND, GameCommand);	//
	}
},
{
	NAME : 'FishPrepControllerCommand'
});