/***/
datatypes.CMD_GR_LogonMobile = {
	"mainCmdID": constVal.MDM_GR_LOGON,
	"subCmdID": constVal.SUB_GR_LOGON_H5_MOBILE,
	"argTypes": [
		{
			"name": "wGameID",
			"type": "UINT16"
		},
        {
			"name": "dwProcessVersion",
			"type": "UINT32"
		},
        {
			"name": "cbDeviceType",
			"type": "UINT8"
		},
        {
			"name": "dwGroupID",
			"type": "UINT32"
		},
        {
			"name": "dwServerID",
			"type": "UINT32"
		},
        {
			"name": "dwUserID",
			"type": "UINT32"
		},
		{
			"name": "szToken",
			"type": "STRING",
			"len": constVal.LEN_TOKEN,
		},
        {
			"name": "szMachineID",
			"type": "STRING",
			"len": constVal.LEN_MACHINE_ID,
		},
        {
			"name": "szAccount",	//uid
			"type": "STRING",
			"len": 32,
		}
	],
	size: function () {
		return 2 + 4 + 1 + 4 + 4 + 4 + (constVal.LEN_TOKEN + constVal.LEN_MACHINE_ID) * 2 + 32 * 2;
	},
	write2bundle: function (bundle, args) {
		bundle.writeUint16(args[0]);
        bundle.writeUint32(args[1]);
        bundle.writeUint8(args[2]);
        bundle.writeUint32(args[3]);
        bundle.writeUint32(args[4]);
        bundle.writeUint32(args[5]);

		bundle.writeString(args[6], constVal.LEN_TOKEN * 2);
        bundle.writeString(args[7], constVal.LEN_MACHINE_ID * 2);
		bundle.writeString(args[8], 32 * 2);
	}
};

