/***/
datatypes.SUB_GR_UserEnter = {
	'mainCmdID': constVal.MDM_GR_USER,
	'subCmdID': constVal.SUB_GR_USER_ENTER,
	'argTypes': [
		{
			'name': 'dwGameID',
            'type': 'UINT32'
		},
		{
			'name': 'dwUserID',
            'type': 'UINT32'
		},
        {
			'name': 'wFaceID',
            'type': 'UINT16'
		},
        {
			'name': 'cbGender',
            'type': 'UINT8'
		},
        {
			'name': 'wTableID',
            'type': 'UINT16'
		},
		{
			'name': 'wChairID',
            'type': 'UINT16'
		},
        {
			'name': 'cbUserStatus',
            'type': 'UINT8'
		},
        {
			'name': 'lScore',
            'type': 'UINT64'
		},
        {
			'name': 'lDiamond',
            'type': 'UINT64'
		},
		{
			'name': 'lExperience',
            'type': 'UINT64'
		},
        {
			'name': 'lLpValues',
            'type': 'UINT64'
		},
        {
			'name': 'lTicket',
            'type': 'UINT64'
		},
        {
			'name': 'dwVipLv',
            'type': 'UINT32'
		},
		{
			'name': 'dwVipPoint',
            'type': 'UINT32'
		},
        {
			'name': 'dwClubPoint',
            'type': 'UINT32'
		},
        {
			'name': 'dwCurExLevel',
            'type': 'UINT32'
		},
        {
			'name': 'dwNextExPoint',
            'type': 'UINT32'
		},
		{
			'name': 'dwIpAddress',
            'type': 'UINT32'
		},
	],

	createFromStream: function (stream, args) {

        let userItem = new Map();

        //
        userItem.set("dwGameID",stream.readUint32());    // I D
        userItem.set("dwUserID",stream.readUint32());    // I D
        //
        userItem.set("wFaceID",stream.readUint16());     //
        //
        userItem.set("cbGender",stream.readUint8());    //
        //
        userItem.set("wTableID",stream.readUint16());    //
        userItem.set("wChairID",stream.readUint16());    //
        userItem.set("cbUserStatus",stream.readUint8());//
        //
        userItem.set("lScore",stream.readInt64().lo);      //
        userItem.set("lDiamond",stream.readInt64().lo);    //
        userItem.set("lExperience",stream.readInt64().lo); //
        userItem.set("lLpValues",stream.readInt64().lo);   //
        userItem.set("lTicket",stream.readInt64().lo);     //
        //vip
        userItem.set("dwVipLv",stream.readUint32());     //
        userItem.set("dwVipPoint",stream.readUint32());  //
        userItem.set("dwClubPoint",stream.readUint32()); //
        //
        userItem.set("dwCurExLevel",stream.readUint32());//
        userItem.set("dwNextExPoint",stream.readUint32());//

        userItem.set("dwIpAddress",stream.readUint32()); //ip

		args.push(userItem);
	}
};

