/***/
datatypes.SUB_GR_TableInfo = {
	'mainCmdID': constVal.MDM_GR_STATUS,
	'subCmdID': constVal.SUB_GR_TABLE_INFO,
	'argTypes': [
		{
			'name': 'cbTableLock',
            'type': 'UINT8'
		},		
        {
			'name': 'cbPlayStatus',
            'type': 'UINT8'
		},	
        {
			'name': 'lCellScore',
            'type': 'UINT32'
		},	
	],

	createFromStream: function (stream, args) {

        let wTableCount = stream.readUint16();     //
        let _tableStatus = []
        for (var i = 0, len = wTableCount; i < len; i++) {
            let tempMap = new Map()   
            let cbTableLock = stream.readUint8();     //  
            let cbPlayStatus = stream.readUint8();     //  
            let lCellScore = stream.readUint32();     //    
            tempMap["cbTableLock"] = cbTableLock
            tempMap["cbPlayStatus"] = cbPlayStatus
            tempMap["lCellScore"] = lCellScore
            _tableStatus[i] = tempMap
        }
        

		args.push(_tableStatus);
	}
};

