let FishGlobalData = require('FishGlobalData');

module.exports = puremvc.define(
	{
		parent: puremvc.Proxy,
		constructor: function () {
			puremvc.Proxy.call(this);
		}
	},
	{
		setFishApp: function (app) {
			FishGlobalData.fishApp = app;
			return app;
		},

		getFishApp: function () {
			return FishGlobalData.fishApp;
		},

		setGameLobbyApp: function (app) {
			FishGlobalData.gameLobbyApp = app;
			return app;
		},

		getGameLobbyApp: function () {
			return FishGlobalData.gameLobbyApp;
		},

		getLogonSuccessData: function () {
			return FishGlobalData.logonSuccessData;
		},

		setLogonSuccessData: function (logonSuccessData) {
			return FishGlobalData.logonSuccessData = logonSuccessData
		},

		getGameServerInfo: function () {
			return FishGlobalData.GameServerInfo;
		},

		setFishServerIpPort: function (ip, port) {
			FishGlobalData.fishServerIp = ip;
			FishGlobalData.fishServerPort = port;
		},

		getFishServerPort: function () {
			return FishGlobalData.fishServerPort;
		},

		getFishServerIp: function () {
			return FishGlobalData.fishServerIp;
		},

		getFishGameKind: function () {
			return FishGlobalData.fishGameKind;
		},

		setUserInfo: function (account, password) {
			FishGlobalData.account = account;
			FishGlobalData.password = password;
		},

		getUserId: function () {
			return FishGlobalData.userId;
		},

		setUserId: function (uesrId) {
			FishGlobalData.userId = uesrId;
		},

		getAccount: function () {
			return FishGlobalData.account;
		},

		getPassword: function () {
			return FishGlobalData.password;
		},

		setTableId: function (tableId) {
			FishGlobalData.tableId = tableId;
		},

		setChairId: function (chairId) {
			FishGlobalData.chairId = chairId;
		},

		getTableId: function () {
			return FishGlobalData.tableId;
		},

		getChairId: function () {
			return FishGlobalData.chairId;
		},

		getWXUserInfo: function () {
			return FishGlobalData.wxUserInfo;
		},

		setWXUserInfo: function (headImgUrl, openId, session_key, unionId, gender, nickName) {
			var wxUserInfo = FishGlobalData.wxUserInfo;
			wxUserInfo.headImgUrl = headImgUrl;
			wxUserInfo.openId = openId;
			wxUserInfo.session_key = session_key;

			wxUserInfo.unionId = unionId;
			wxUserInfo.gender = gender;
			wxUserInfo.nickName = nickName;
		},

		getNickName: function () {
			return FishGlobalData.wxUserInfo.nickName || FishGlobalData.account;
		},

		// getNickName: function () {
		// 	return FishGlobalData.wxUserInfo.nickName ;
		// },

		getHeadImgUrl: function () {
			return FishGlobalData.wxUserInfo.headImgUrl;
		},

		getOpenId: function () {
			return FishGlobalData.wxUserInfo.openId;
		},

		getUnionId: function () {
			return FishGlobalData.wxUserInfo.unionId;
		},

		setNewUserGuideStep: function (newUserGuideStep) {
			FishGlobalData.newUserGuideStep = newUserGuideStep;
		},

		getNewUserGuideStep: function () {
			return FishGlobalData.newUserGuideStep;
		},

		setInviteUserID: function (InviteUserID) {
			FishGlobalData.InviteUserID = InviteUserID;
		},

		getInviteUserID: function () {
			return FishGlobalData.InviteUserID;
		},
		
		setNewUserWelfareLayerData: function (newUserWelfareLayerData) {
			FishGlobalData.newUserWelfareLayerData = newUserWelfareLayerData;
		},

		getNewUserWelfareLayerData: function () {
			return FishGlobalData.newUserWelfareLayerData;
		},

		setNewUserMissionLayerData: function (newUserMissionLayerData) {
			FishGlobalData.newUserMissionLayerData = newUserMissionLayerData;
		},

		getNewUserMissionLayerData: function () {
			return FishGlobalData.newUserMissionLayerData;
		},

	},
	{
		NAME: 'FishGlobalDataProxy'
	});