
module.exports = puremvc.define(
	{
		parent: puremvc.SimpleCommand
	},
	{
		execute: function (tableCmd) {
			let GameTableProxy = this.facade.retrieveProxy('GameTableProxy');
            //FishConstants.GAME_TABLE_COMMAND
            switch(tableCmd) {
                case "sendStandUpData":
                    GameTableProxy.sendStandUpData(1);
                	break;
                case "sendUserReady":
                    GameTableProxy.sendUserReady();
                	break;
                default:
                    cc.log('');
           } 




		}
	},
	{
		NAME: 'GameTableCommand'
	});