let appFacade = require('FishFacade');

cc.Class({
    extends: cc.Component,

    properties: {
        Button_back: {
            type: cc.Button,
            default: null,
        },
        Button_setting: {
            type: cc.Button,
            default: null,
        },
        FileNode_setting: {
            type: cc.Node,
            default: null,
        },
        Panel_bg: {
            type: cc.Node,
            default: null,
        },
        Button_spin: {
            type: cc.Button,
            default: null,
        },
        PanelTimes: {
            type: cc.Node,
            default: null,
        },
        Text_score: {
            type: cc.Label,
            default: null,
        },
        Text_total_bet: {
            type: cc.Label,
            default: null,
        },
        Text_total_win: {
            type: cc.Label,
            default: null,
        },
        Text_time_left: {
            type: cc.Label,
            default: null,
        },
        Button_sub_bet: {
            type: cc.Button,
            default: null,
        },
        Button_add_bet: {
            type: cc.Button,
            default: null,
        },
        Button_max_bet: {
            type: cc.Button,
            default: null,
        },
        Button_stop_spin: {
            type: cc.Button,
            default: null,
        },

        Button_times_10: {
            type: cc.Button,
            default: null,
        },
        Button_times_20: {
            type: cc.Button,
            default: null,
        },
        Button_times_50: {
            type: cc.Button,
            default: null,
        },
        Button_times_100: {
            type: cc.Button,
            default: null,
        },
        Button_times_500: {
            type: cc.Button,
            default: null,
        },
        Button_times_fast: {
            type: cc.Button,
            default: null,
        },
        Sprite_total_bet: {
            type: cc.Node,
            default: null,
        },

        Button_rule: {
            type: cc.Node,
            default: null,
        },

        Button_sound: {
            type: cc.Toggle,
            default: null,
        },
        Button_music: {
            type: cc.Toggle,
            default: null,
        },
        
        
        
    },


    onLoad () {

        this._btnBack = this.Button_back;
        this._btnSetting = this.Button_setting
        this._nodeSetting = this.FileNode_setting
        this._btnSettingRule = this.Button_rule

        this._btnSound = this.Button_sound
        this._btnMusic = this.Button_music

        this._panelTimes = this.PanelTimes
        this._textUserScore = this.Text_score
        this._textTotalBet = this.Text_total_bet
        this._textTotalWin = this.Text_total_win
        this._textTimesLeft = this.Text_time_left

        
        this._btnSubBet = this.Button_sub_bet
        this._btnAddBet = this.Button_add_bet
        this._btnMaxBet = this.Button_max_bet
        this._btnStartSpin = this.Button_spin
        this._btnStopSpin = this.Button_stop_spin
        let btnTimes10 = this.Button_times_10
        let btnTimes20 = this.Button_times_20
        let btnTimes50 = this.Button_times_50
        let btnTimes100 = this.Button_times_100
        let btnTimes500 = this.Button_times_500
        let btn_times_fast = this.Button_times_fast

        


        this.node.on(cc.Node.EventType.TOUCH_START, this.onTouchBegan, this);
        this._btnBack.node.on('click', this.BackButtonClickEvent, this);
        this._btnSetting.node.on('click', this.SettingButtonClickEvent, this);
        this._btnSettingRule.on('click', this.SettingRuleButtonClickEvent, this);

        this._btnSound.node.on('click', this.onClickSound, this);
        this._btnMusic.node.on('click', this.onClickMusic, this);
        ///////////////////////////////////////////////////////
        // 
        this._btnStartSpin.node.on(cc.Node.EventType.TOUCH_START, this.SpinButtonClickEvent, this);
        this._btnStartSpin.node.on(cc.Node.EventType.TOUCH_END, this.SpinButtonClickEvent, this);
        this._btnStartSpin.node.on(cc.Node.EventType.TOUCH_CANCEL, this.SpinButtonClickEvent, this);
        //
        this._btnStopSpin.node.on('click', this.onClickStopSpin, this);
        
        btnTimes10.node.on('click', this.onClickAutoSpin, this);
        btnTimes20.node.on('click', this.onClickAutoSpin, this);
        btnTimes50.node.on('click', this.onClickAutoSpin, this);
        btnTimes100.node.on('click', this.onClickAutoSpin, this);
        btnTimes500.node.on('click', this.onClickAutoSpin, this);
        btn_times_fast.node.on('click', this.onClickAutoSpin, this);

        this._btnSubBet.node.on('click', this.onClickSubBet, this);
        this._btnAddBet.node.on('click', this.onClickAddBet, this);
        this._btnMaxBet.node.on('click', this.onClickMacBet, this);

        //////////////////////////////////////////////////////////////

        appFacade.registerMediator(this);
        this._globalDataProxy = appFacade.retrieveProxy('FishGlobalDataProxy');

        this.updateUIofUserInfo()
        
    },

    listNotificationInterests: function () {
        return [{
            evtType: "SHOW_DIALOG", //
            callback: this.showDialog
        },
        {
            evtType: "UPDATE_UI_OF_USER_INFO", //UI
            callback: this.updateUIofUserInfo
        },
        {
            evtType: "SET_TOTAL_WIN", //
            callback: this.setTotalWin
        },
        {
            evtType: "CHANGE_GAME_BTN_STATE", //
            callback: this.changeGameBtnState
        },
        {
            evtType: "SET_TOTAL_BET", //
            callback: this.setTotalBet
        },
        {
            evtType: "CHANGE_SPIN_BTN_STATE", //
            callback: this.changeSpinBtnState
        },
        {
            evtType: "UPDATE_SCORE", //
            callback: this.updateScore
        },
        {
            evtType: "SHOW_TOTAL_TEXT", //
            callback: this.showTotalText
        },
        {
            evtType: "SET_AUTO_PLAY_REMAINING", //
            callback: this.setAutoplayRemaining
        },
        {
            evtType: "SHOW_TOTAL_BET", //
            callback: this.showTotal_bet
        },
        ];
        
    },

    start () {
        
        this.node.parent.parent.getComponent("GameView").GameUIManager.attachUI(this.node.parent.parent)
    },

    showDialog: function (message,func){
        this.node.parent.parent.getComponent("GameView").GameUIManager.openTip(message,func)
    },

    showTotal_bet(bshow){
        this.Sprite_total_bet.opacity = bshow ? 255 : 0
    },

    // 
    setTotalBet(_num){
        let num = Help.checknumber(_num)
        if (this._textTotalBet) {
            //this._textTotalBet.string = Help.FormatScore(num)
            this._textTotalBet.string = num / SCORE_RATIO;
            
        }
    },

    // UI
    updateUIofUserInfo(){
        if (this._textUserScore) {
            this._textUserScore.string = Help.FormatScore(this._globalDataProxy.getLogonSuccessData().lScore)
        }

    },

    updateScore(value){
        if (this._textUserScore) {
            this._globalDataProxy.getLogonSuccessData().lScore = this._globalDataProxy.getLogonSuccessData().lScore + value
            this._textUserScore.string = Help.FormatScore(this._globalDataProxy.getLogonSuccessData().lScore)
        }
    },

    onTouchBegan: function (event){
        if(this._nodeSetting.active === true){
            let camera=cc.Camera.main;
            
            let nsp = this._nodeSetting.convertToNodeSpaceAR(camera.getScreenToWorldPoint(event.getLocation()));
            
            let rect = new cc.Rect(this.Panel_bg.x,this.Panel_bg.y,this.Panel_bg.width,this.Panel_bg.height);
            
            if(!rect.contains(nsp)){
                this.hideSettingDialog()
            }; 
                 
        }

        if (this._panelTimes && this._panelTimes.active) {
            let camera=cc.Camera.main;
            let nsp = this._panelTimes.parent.convertToNodeSpaceAR(camera.getScreenToWorldPoint(event.getLocation()));
            let rect = new cc.Rect(this._panelTimes.x,this._panelTimes.y,this._panelTimes.width,this._panelTimes.height);
            if(!rect.contains(nsp)){
                this._panelTimes.active = false
            }
        }

    },

    BackButtonClickEvent: function () {
        
        //appFacade.sendNotification("SetFreeEnterBackAni",1);
        appFacade.sendNotification(FishConstants.GAME_TABLE_COMMAND,"sendStandUpData");
    },

    SettingButtonClickEvent: function (event) {
        if (!this._nodeSetting.active) {
            this.updateUIofSetting()
            this._nodeSetting.active = true;
            Help.popDialog(this._nodeSetting)
            
        };
    },

    // 
    onClickSound(){
        if (this.node.parent.parent.getComponent("GameView").GameUIManager.BooleffectV){
            this.node.parent.parent.getComponent("GameView").GameUIManager.setEffectV(0)
        }else{
            this.node.parent.parent.getComponent("GameView").GameUIManager.setEffectV(1)
        }
    },

    // 
    onClickMusic(){
        if (this.node.parent.parent.getComponent("GameView").GameUIManager.BoolmusicV){
            this.node.parent.parent.getComponent("GameView").GameUIManager.setMusicV(0)
        }else{
            this.node.parent.parent.getComponent("GameView").GameUIManager.setMusicV(1)
        }
        //self:updateUIofSetting()
    },

    // 
    updateUIofSetting(){

        if (this._btnSound) {
            this._btnSound.isChecked = this.node.parent.parent.getComponent("GameView").GameUIManager.BooleffectV
        }

        if (this._btnMusic) {
            this._btnMusic.isChecked = this.node.parent.parent.getComponent("GameView").GameUIManager.BoolmusicV
        }
    },

    SettingRuleButtonClickEvent: function (event) {
        //this.node.parent.parent.getComponent("GameView").GameHelpLayer_Node.active = true
        this.node.parent.parent.getComponent("GameView").GameUIManager.openUI(3)
    },

    SpinButtonClickEvent: function (event) {

        if (event.type === "touchstart") {
            this._holdTime = 0
            this.timer = cc.director.getScheduler()
            this.timer.schedule(this.CalculateTime, this, 0, false);
            

        }else if (event.type === "touchend"){
            if(this.timer){
                this.timer.unschedule(this.CalculateTime,this);
                this.timer = null
            }
            if (this._holdTime > 60) {
                return
            }
            this.onClickStartSpin()
        }else if (event.type === "touchcancel"){
            if(this.timer){
                this.timer.unschedule(this.CalculateTime,this);
                this.timer = null
            }
        }
    },  
    
    CalculateTime(dt){
        this._holdTime = this._holdTime + 1
        if (this._holdTime > 60) {
            this._panelTimes.active = true
        }
    },
 
    hideSettingDialog: function () {
        if (this._nodeSetting) {
            
            if (this._nodeSetting.active){

                Help.hideDialog(this._nodeSetting, null, function(){
                    this._nodeSetting.active = false;
                }.bind(this))
                
            }
            
            
        };
    },

    // 
    onClickSubBet(){
        appFacade.sendNotification(FishConstants.GAME_COMMAND,"subBet");
    },

    // 
    onClickAddBet(){
        appFacade.sendNotification(FishConstants.GAME_COMMAND,"addBet");
    },

    // 
    onClickMacBet(){
        appFacade.sendNotification(FishConstants.GAME_COMMAND,"maxBet");
    },
    
    // 
    onClickStartSpin(){
        appFacade.sendNotification(FishConstants.GAME_COMMAND,"startSpin");
    },

    // 
    onClickStopSpin(){

        cc.log("click stop=========")
        appFacade.sendNotification(FishConstants.GAME_COMMAND,"stopSpin");
        this._btnStopSpin.interactable = false
        this.scheduleOnce(function(){
            this._btnStopSpin.interactable = true
        }.bind(this), 2)

    },

    //
    onClickAutoSpin(event){
        let times = 0
        if(event.node.name == "Button_times_10"){
            times = 10
        }else if (event.node.name == "Button_times_20"){
            times = 20
        }else if (event.node.name == "Button_times_50"){
            times = 50
        }else if (event.node.name == "Button_times_100"){
            times = 100
        }else if (event.node.name == "Button_times_500"){
            times = 500
        }else if (event.node.name == "Button_times_fast"){
            times = 10000000
        }

        this._panelTimes.active = false
        appFacade.sendNotification(FishConstants.GAME_COMMAND,"autoSpin",times);
    },

    // 
    setAutoplayRemaining(_count){
        let count = Help.checknumber(_count)
        if (this._textTimesLeft && count > 0) {
            this._textTimesLeft.node.active = true
            if (count > 500) {
                this._textTimesLeft.string = ""
            }
            else{
                this._textTimesLeft.string = count
            }
        }
        else{
            this._textTimesLeft.node.active = false
            this._textTimesLeft.string = 0
        }
    },

    //
    setTotalWin(num){
        num = Help.checknumber(num)
        if (this._textTotalWin) {

            let _oldScore = Number(this._textTotalWin.string)
            if (_oldScore === null || _oldScore < 0) {
                _oldScore = 0
            }

            _oldScore = _oldScore * SCORE_RATIO

            if (num > _oldScore) {
                cc.log("old:" + _oldScore + " new:" + num)
                Help.scorllNumber(this._textTotalWin.node, _oldScore, num, 0.5, function(node,num){
                    this._textTotalWin.string = Help.FormatScore2(num)
                    return
                }.bind(this))
            }
            else{
                //0
                if (num <= 0) {
                    this._textTotalWin.string = ""
                    return
                }
            }
            this._textTotalWin.string = Help.FormatScore2(num)
        }
    },

    // spin or stop
    changeSpinBtnState(spin){

        if (this._btnStartSpin) {
            this._btnStartSpin.node.active = spin
        }
    
        if (this._btnStopSpin) {
            this._btnStopSpin.node.active = !spin
        }
    },

    // 
    changeGameBtnState(enable){

        let enableColor = new cc.Color(255, 255, 255)
        let disableColor = new cc.Color(188, 188, 188)

        if (this._btnBack) {
            this._btnBack.interactable = enable
            if (enable) {
                this._btnBack.node.color = enableColor
            }
            else{
                this._btnBack.node.color = disableColor
            }
        }

        if (this._btnStartSpin) {
            this._btnStartSpin.interactable = enable
            if  (enable) {
                this._btnStartSpin.node.on(cc.Node.EventType.TOUCH_START, this.SpinButtonClickEvent, this);
                this._btnStartSpin.node.on(cc.Node.EventType.TOUCH_END, this.SpinButtonClickEvent, this);
                this._btnStartSpin.node.on(cc.Node.EventType.TOUCH_CANCEL, this.SpinButtonClickEvent, this);
            }else{
                this._btnStartSpin.node.off(cc.Node.EventType.TOUCH_START, this.SpinButtonClickEvent, this);
                this._btnStartSpin.node.off(cc.Node.EventType.TOUCH_END, this.SpinButtonClickEvent, this);
                this._btnStartSpin.node.off(cc.Node.EventType.TOUCH_CANCEL, this.SpinButtonClickEvent, this);
            }


            if (enable) {
                this._btnStartSpin.node.color = enableColor
            }
            else{
                this._btnStartSpin.node.color = disableColor
            }
        }

        if (this._btnSubBet) {
            this._btnSubBet.interactable = enable
            if (enable) {
                this._btnSubBet.node.color = enableColor
            }
            else{
                this._btnSubBet.node.color = disableColor
            }
        }

        if (this._btnAddBet) {
            this._btnAddBet.interactable = enable
            if (enable) {
                this._btnAddBet.node.color = enableColor
            }
            else{
                this._btnAddBet.node.color = disableColor
            }
        }

        if (this._btnMaxBet) {
            this._btnMaxBet.interactable = enable
            if (enable) {
                this._btnMaxBet.node.color = enableColor
            }
            else{
                this._btnMaxBet.node.color = disableColor
            }
        }
    },

    showTotalText(bshow){
        if (this._textTotalWin) {
            this._textTotalWin.node.opacity = bshow ? 255 : 0
        }
    },


    // update (dt) {},

    onDestroy: function () {
		appFacade.removeMediator(this.name);
	},
});
