
module.exports = puremvc.define(
	{
		parent: puremvc.SimpleCommand
	},
	{
		execute: function () {
			let GameProxy = this.facade.retrieveProxy('GameProxy');
            //FishConstants.GAME_COMMAND
            switch(arguments[0]) {
                case "startSpin":
                	GameProxy.startSpin();
                	break;
				case "onGetNoticeReady":
                	GameProxy.onGetNoticeReady();
                	break;
				case "stopSpin":
					GameProxy.stopSpin();
					break;
				case "autoSpin":
					GameProxy.autoSpin(arguments[1]);
					break;	
				case "subBet":
					GameProxy.subBet();
					break;	
				case "addBet":
					GameProxy.addBet();
					break;
				case "maxBet":
					GameProxy.maxBet();
					break;											
                default:
                    cc.log('');
           } 


		   

		}
	},
	{
		NAME: 'GameCommand'
	});