
let gameModel = require('gameModel');
let GameUIManager = require('gameUIManager');appFacade
let appFacade = require('FishFacade');

const i18n = require('i18n');

module.exports = puremvc.define(
	{
		parent: puremvc.Proxy,
		constructor: function () {
			puremvc.Proxy.call(this, this.constructor.NAME);
		}
	},
	{
		onRegister: function () {

            this.gameModel = new gameModel(this.gameConfig,this);
			this._globalDataProxy = this.facade.retrieveProxy('FishGlobalDataProxy');
			
		},

		registerNetInterface: function (app,gameinfo) {

            this.app = app;
		    let net = app.net;
		    this.net = net;
			//this.gameUIManager = new GameUIManager(gameinfo.wKindID);
			this.gameUIManager = new GameUIManager(520);

		},

		//reel cell
		updateCellSprite(cell, col, isGetResult, cellKey, isReset){
			if (cellKey != null) {
				
				this.sendNotification("GAMEVIEW_UPDATE_CELL_SPRITE",cell, cellKey, col, isGetResult, isReset);
				return null
			}
		
			let theCellKey = null
			if (isGetResult && this.gameModel._resultItems[col] && this.gameModel._resultItems[col].length > 0) {
				theCellKey = this.gameModel._resultItems[col].pop();
			}
		
			if (theCellKey === null) {
				theCellKey = this.gameModel.getNextSprite(col, cellKey)
			}
			
			this.sendNotification("GAMEVIEW_UPDATE_CELL_SPRITE",cell, theCellKey, col, isGetResult, isReset);

		},


		//////////////////////////////////////////////////////////// spin /////////////////////////////////////////////
		//
		startSpin(){
			if (this.gameModel._autoSpin > 0) {
				return
			}
			if (!this._gameView){
				this._gameView = appFacade.retrieveMediator("GameView")
			}
			
			this.spin()
			
		},

		//
		autoSpin(times){
			if (this.gameModel._autoSpin <= 0){
				this.gameModel._autoSpin = Help.checknumber(times) || 0
				if (this.gameModel._autoSpin >= 0) {
					this.gameModel._autoSpin = this.gameModel._autoSpin - 1
					this.sendNotification("SET_AUTO_PLAY_REMAINING",this.gameModel._autoSpin);
					this.spin()
				}
			}
			else{
				this.gameModel._autoSpin = Help.checknumber(times) || 0
			}
		},
		
		//
		stopSpin(){
		
			this.fastStop()
		},

		//
		cleanStatusWithSpin(){
			this.gameModel._spinResult = null
			this.gameModel._totalWin = 0
		
			this.clearAnimate()
			this.cleanStatus()
		},

		
		//
		clearAnimate(){
			this.stopDrawAnimate()
		},

		//
		cleanStatus(){
			this.speedUpState = null
		},

		//
		spin(){
			let totalBet = this.gameModel.getTotalBet()
			if (this.gameModel._freeSpin < 1) {

				if (this._globalDataProxy.getLogonSuccessData().lScore < totalBet) {
					//
					// showDialog(Multilingual(""), enum.DialogShowBtn.OK, function()
					// 	if this._gameLayer && this._gameLayer.onClickShop then
					// 		this._gameLayer:onClickShop()
					// 	end
					// end)
					this.sendNotification("SHOW_DIALOG",["Se voc no tiver ouro suficiente, saia do quarto para recarregar"]);
					return
				}

				this._globalDataProxy.getLogonSuccessData().lScore = this._globalDataProxy.getLogonSuccessData().lScore - totalBet
				this.sendNotification("UPDATE_UI_OF_USER_INFO");

			}
		
			this.sendNotification("SHOW_TOTAL_TEXT",false);
			this.cleanStatusWithSpin()
		
			// totalwin
			if (this.gameModel._freeSpin < 1) {
				this.sendNotification("CHANGE_GAME_BTN_STATE",false);
				this.sendNotification("SET_TOTAL_WIN",this.gameModel._totalWin);
			}
		
			this.spinBoard()
		
			this.requestSpin(this.gameModel._lineCount, this.gameModel._betIndex)
		},

		
		//
		spinBoard(){
						
			this.gameUIManager.playAudio("start",false)
			if (!this._gameView){
				this._gameView = appFacade.retrieveMediator("GameView")
			}
			this.CurSpinLayer = this._gameView.getCurSpinLayer();
			this.CurSpinLayer.spin(this.getSpinConfig())
		},

		getSpinConfig(){
			
			let spinConfig = []

			for (let [col,reel] of Object.entries(this.CurSpinLayer.spinReels)){
				let theStartAction = this.getSpinColStartAction(col)
				let theReelConfig = {
					["col"] : col,
					["action"] : theStartAction,
				}
				spinConfig.push(theReelConfig)
			}
			return spinConfig
		},

		//
		getSpinColStartAction(col){

			let reelSpinConfig = this.gameConfig.reelSpinConfig
		
			let spinAction = {}
			spinAction.delay            = reelSpinConfig.spinDelay * (col - 1)
			spinAction.speedUpTime      = reelSpinConfig.speedUpTime
			spinAction.maxSpeed         = reelSpinConfig.maxSpeed
			spinAction.upBounce         = reelSpinConfig.upBounce
			spinAction.upBounceTime     = reelSpinConfig.upBounceTime
			spinAction.upBounceMaxSpeed = reelSpinConfig.upBounceMaxSpeed
		
			return spinAction
		},

		//
		createFreeRewardLayer(){
			let recvReward = function(){
				this.freeGameEnd()
			}.bind(this)
		
			if (this._gameView && this._gameView.createFreeRewardLayer) {
				this._gameView.createFreeRewardLayer(this.gameModel._freewin, recvReward)
			}
		},
		
		/////////////////////////////////////////////////////////////// stop ////////////////////////////////////////////////////////
		
		//
		normalStop(){
			if (null === this.gameModel._spinResult) {
				return
			}
		
			this.stop(this.gameModel._spinResult)
		},

		//
		fastStop(){
			if (null === this.gameModel._spinResult) {
				return
			}
		
			if (this.gameModel._autoSpin > 0) {
				this.gameModel._autoSpin = 0
			}
			this.fastStopBoard()
		},

		//
		stop(data){
			this.gameModel.adjustRecData(data)
			this.genSpecialSymbolState(data)
			this.stopReels(data)
		},	

		//
		genSpecialSymbolState(data){
			this.speedUpState = [] // 
			this.notifyState = []  // symbol
		
			let cItemList = data["item_list"]
			let checkConfig = this.gameModel.specialSymbolConfig
			for (let [itemKey,theItemConfig] of Object.entries(checkConfig)){
				let itemType = theItemConfig["type"]
				let itemCnt = 0
				let isBreak = false
				let baseColCnt = 5
				if (itemType) {
					for (let col = 0;col < this._gameView.getCurSpinLayer().spinReels.length;col++) {
						let colItemList = cItemList[col]
						let colRowCnt = this._gameView.getCurSpinLayer().spinReels[col].row
						let curColMaxCnt = theItemConfig["col_set"][col] || colRowCnt
		
						// 
						if (curColMaxCnt > 0 && (itemCnt + curColMaxCnt) >= theItemConfig["min_cnt"]) {
							this.speedUpState[col] = this.speedUpState[col] || {}
							this.speedUpState[col][itemKey] = true
						}
		
						// 
						for (let [row,theItem] of Object.entries(colItemList)){
							if (theItem == itemKey) {
								this.notifyState[col] = this.notifyState[col] || []
								this.notifyState[col][itemKey] = this.notifyState[col][itemKey] || []
								this.notifyState[col][itemKey].push([col, row])
							}
						}

						for (let [row,theItem] of Object.entries(colItemList)){
							if (theItem === itemKey) {
								itemCnt = itemCnt + 1
							}
						}
					}
				}
			}
		
		},
		
		//
		stopReels(data){
			let endCallFunc = function(){
				let nextCallFunc = function(){
					this.spinning = false
					this.handleResult()
				}.bind(this)
		
				if (this.stopEndFunc) {
					this.stopEndFunc(nextCallFunc)
					this.stopEndFunc = null
				}
				else{
					nextCallFunc()
				}
			}.bind(this)
		
			this.stopBoard(data, endCallFunc)
		},

		stopBoard(data, endCallFunc){
			this._gameView.getCurSpinLayer().stop(this.getStopConfig(), data["item_list"], data["item_list_up"], data["item_list_down"], endCallFunc)
		},

		fastStopBoard(){
			cc.log("fast stop==============")
			this._gameView.getCurSpinLayer().fastStop()
		},

		// stop
		getStopConfig(col){
			let stopConfig = []
			for (let [col,_] of Object.entries(this._gameView.getCurSpinLayer().spinReels)) {
				let theStopAction = this.getSpinColStopAction(col)
				let theReelConfig = {
					["col"] : col,
					["action"] : theStopAction
				}
				stopConfig.push(theReelConfig)
			}
			return stopConfig
		},	

		getSpinColStopAction(col){
			let reelStopConfig = this.gameConfig.reelSpinConfig

			let spinAction = {}

			spinAction.stopDelay            = (col - 1) * reelStopConfig.stopDelay
			spinAction.speedDownTime        = reelStopConfig.speedDownTime
			spinAction.maxSpeed             = reelStopConfig.maxSpeed
			spinAction.downBounce           = reelStopConfig.downBounce
			spinAction.downBounceMaxSpeed   = reelStopConfig.downBounceMaxSpeed
			spinAction.downBounceTime       = reelStopConfig.downBounceTime
			spinAction.stopType             = 0

			return spinAction
		},	
		
		//
		checkSpeedUp(checkCol){
			let isNeedSpeedUp = false
			if (this.speedUpState && this.speedUpState[checkCol] && this.speedUpState[checkCol].length > 0) {
				isNeedSpeedUp = true
			}
			return isNeedSpeedUp
		},

		// 
		playReelNotifyEffect(col){
		},
		
		// 
		stopReelNotifyEffect(col){
		},
		
		// ()
		onReelFallBottom(col){
		},
		
		// ()
		onReelFastFallBottom(col){
		},

		//
		onReelStop(col){
			if (this.notifyState && this.notifyState[col] && this.notifyState[col].length > 0) {
				this.playSpecialSymbolStopAni(col, this.notifyState[col])
			}
		},
		
		playSpecialSymbolStopAni(col, symbols){
			this._gameView.playSpecialSymbolStopAni(col, symbols)
		},		

		// 
		dealSceneData(data){
			this.gameModel._baseScore = data["baseScore"]
			this.gameModel._betCount = data["betCount"]
			this.gameModel._betList = data["betList"]
			this.gameModel._betIndex = data["bet_index"]
			
			if (data["line_count"]) {
				this.gameModel._lineCount = data["line_count"]
			}

			if (data["item_list"]) {
				this.gameModel._spinResult = data
				this.gameModel._totalWin = this.gameModel._spinResult["total_win"]
				this.gameModel._freeSpin = this.gameModel._spinResult["free_spin"]
				this.gameModel._freewin  = this.gameModel._spinResult["free_win"]
				this.gameModel._freeState = this.gameModel._spinResult["free_state"]
				this.gameModel._betIndex = this.gameModel._spinResult["bet_index"]
				this.gameModel._lineCount = this.gameModel._spinResult["line_count"]

				this._globalDataProxy.getLogonSuccessData().lScore = this._globalDataProxy.getLogonSuccessData().lScore - this.gameModel.getTotalBet()
				this.sendNotification("UPDATE_UI_OF_USER_INFO");
				this.sendNotification("CHANGE_GAME_BTN_STATE",false);
				this.sendNotification("SET_TOTAL_BET",this.gameModel.getTotalBet());
				
				this.gameModel.adjustRecData(this.gameModel._spinResult)
				this.recoveryScene(this.gameModel._spinResult)
			}
			else{
				// 
				this.sendNotification("SET_TOTAL_BET",this.gameModel.getTotalBet());
				// 
				this.sendUserReady()
			}
		},

		// spin
		dealRequestSpinResult(data_){

			let data = this.gameModel.AddFakeData(data_)
			if (!this.gameModel._spinResult) {
				this.gameModel._spinResult = data
			}
		
			this.gameModel._totalWin = this.gameModel._spinResult["total_win"]
			this.gameModel._freeSpin = this.gameModel._spinResult["free_spin"]
			this.gameModel._freewin  = this.gameModel._spinResult["free_win"]
			this.gameModel._freeState = this.gameModel._spinResult["free_state"]
		
			if (this.gameModel._autoSpin > 500) {
				this.sendNotification("CHANGE_SPIN_BTN_STATE",false);
				this.normalStop()
			}
			else{
				this._gameView.scheduleOnce(function(){
					if (this.gameModel._autoSpin <= 500) {
						this.sendNotification("CHANGE_SPIN_BTN_STATE",false);
						this.normalStop()
					}
					else{
						if (this.gameModel._autoSpin > 0) {
							this.fastStopBoard()
						}
					}
				}.bind(this), 1)
			}
		
		},
		

		// 
		handleResult(){
			this.drawAnimate(this.gameModel._spinResult)

			this.sendNotification("CHANGE_SPIN_BTN_STATE",true);

			if (this.gameModel._freeState === 1 && this.gameModel._freeSpin > 0) {
				//todo
				// performWithDelay(this._gameScene, function()
				// 	this.createStartFreeLayer()
				// end, 2)
			}
			else if (this.gameModel._freeState === 3 && this.gameModel._freeSpin < 1) {
				//spin
				let delay = 0
				if (this.gameModel._totalWin > 0) {
					delay = 1
				}

				this.gameModel._curwinscore = this.gameModel._freewin
				//todo
				// performWithDelay(this._gameScene, function()
				// 	this._gameLayer:setFreeGameFireVisible(false)
				// 	this.createFreeRewardLayer()
				// end, delay)

			}

			// 
			if (this.gameModel._freeSpin < 1 && this.gameModel._freeState != 3) {
				//spin
				this.sendNotification("SET_TOTAL_WIN",this.gameModel._totalWin);
				this.sendNotification("UPDATE_SCORE",this.gameModel._totalWin);
				this.sendNotification("CHANGE_GAME_BTN_STATE",true);

				this.gameModel._curwinscore = this.gameModel._totalWin
			}
			else{			
				this.sendNotification("SET_TOTAL_WIN",this.gameModel._freewin);
			}

			if (this.gameModel._freeState != 3) {
				this.requestStop()
			}

			if (this.gameModel._freeState === 2 && this.gameModel._freeSpin > 0) {
				let delay = 0.3
				if (this.gameModel._totalWin > 0) {
					delay = 1
				}
				//todo
				// performWithDelay(this._gameScene, function()
				// 	this.freeGameStart()
				// end, delay)
			}
		},

		// 
		dealRequestStopResult(){
			let _Wincallbackfun = null
			if (this.gameModel._autoSpin && this.gameModel._autoSpin > 0) {
				let delay = 0.3
				if (this.gameModel._totalWin > 0) {
					delay = 1
				}

				let timeCallback = function (dt) {
					this.gameModel._autoSpin = this.gameModel._autoSpin - 1
					this.sendNotification("SET_AUTO_PLAY_REMAINING",this.gameModel._autoSpin);
					this.spin()
				}.bind(this)
				this._gameView.scheduleOnce(timeCallback, delay);
				//todo
				// _Wincallbackfun = function(){
				// 	let timeCallback = function (dt) {
				// 		this.gameModel._autoSpin = this.gameModel._autoSpin - 1
				// 		this.sendNotification("SET_AUTO_PLAY_REMAINING",this.gameModel._autoSpin);
				// 		this.spin()
				// 	}.bind(this)
				// 	this.scheduleOnce(timeCallback, delay);
				// }
			}
			else{
				this.sendNotification("SET_AUTO_PLAY_REMAINING",0);
			}

			if (this.gameModel._curwinscore/this.gameModel.getTotalBet() >= 5) {
				if (_Wincallbackfun === null) {
					_Wincallbackfun = function(){
						this.sendNotification("SHOW_TOTAL_TEXT",true);
					}  
				}
				this.sendNotification("SHOW_TOTAL_TEXT",false);
			}

			///////
			this.sendNotification("SHOW_TOTAL_TEXT",true);
			// setTimeout(function(){
			// 	this.sendNotification("SHOW_TOTAL_TEXT",false);
			// }.bind(this), 4000)
			
			/////////

			//todo
			//this._gameLayer:checkWinShow(this._curwinscore,this:getTotalBet(),_Wincallbackfun)

			this._curwinscore = 0
		},

		/// 
		drawAnimate(data){
			if (data === null) {
				cc.log("data error.............")
				cc.log(data)
			}
		
			if (data && data["win_lines"] && data["win_lines"].length > 0) {
				this.drawLinesThemeAnimate(this.gameModel.winLines, this._gameView.getCurSpinLayer(), data)
			}
		},

		// 
		stopDrawAnimate(){
			this.sendNotification("STOP_DRAW_ANIMATE");
		},

		drawLinesThemeAnimate(lines, layer, rets){
			this.sendNotification("DRAW_LINES_THEME_ANIMATE",lines, layer, rets);
		},		

		/////////////////////////////////////////////////////////////// free game ///////////////////////////////////////////////////////////////
		freeGameStart(){
			// this._gameLayer:setFreeGameFireVisible(true)
			// if this._gameView && this._gameView.setFreeRemain then
			// 	this._gameView.setFreeRemain(this.gameModel._freeSpin - 1)
			// end
			// this.spin()
		},
		
		freeGameEnd(){
			
			this.sendNotification("UPDATE_SCORE",this.gameModel._freewin);
			this.sendNotification("CHANGE_GAME_BTN_STATE",true);
			this.gameModel._freewin = 0
			this.requestStop()
		},
		
		recoveryScene(data){

			let intervalId = setInterval(function() {
				if (appFacade.retrieveMediator("GameView")) {
					if (appFacade.retrieveMediator("GameView").IsinitSpinLayer) {
						this.sendNotification("CHANGE_GAME_BTN_STATE",false);
						for (let [col,keys] of Object.entries(data["item_list"])) {
							for (let [row,key] of Object.entries(keys)) {
								let cell = this._gameView.getCurSpinLayer().getCell(col, row)

								// let labnode = new cc.Node()
								// labnode.color = cc.Color.RED
								// labnode.setScale(0.7)
								// let lab = labnode.addComponent(cc.Label)
								// lab.string = "col:" + col + "   " + "row:" + row
								// cell.addChild(labnode)

								this.updateCellSprite(cell, col, true, key)
								cc.log("recoveryScene")
							}
						}
			
						this.handleResult()
					
						clearInterval(intervalId);
						
					}
				}
				
			}.bind(this), 100);

		},

		/////////////////////////////////////////////////////////////// network ///////////////////////////////////////////////////////////////
		
		// 
		sendUserReady(){
			appFacade.sendNotification(FishConstants.GAME_TABLE_COMMAND,"sendUserReady");
		},

		// spin
		requestSpin(lineNum, betNum){
		},

		// 
		requestStop(){
		},

		//
		onEventGameScene: function (gameStatus) {
			cc.log('');
		},

		//
		onGetNoticeReady(){
    		this.sendUserReady()
		},
	},
	{
		NAME: 'GameProxy'
	});