                // elseif rand > 10 && rand <= 15 then
                //     this.m_parent._gameCtrl:playEffect("collect_dia_vo3")
                // elseif rand > 15 && rand <= 20 then
                //     this.m_parent._gameCtrl:playEffect("collect_dia_vo4")
                // end
                
                //this.m_parent._gameCtrl:playEffect("dialog_pop")
    
