        //     var width = event.width;
        //     var height = event.height;
        //     var designWidth = 1334;
        //     var designHeight = 750;
        //     var scaleWidth = width / designWidth;
        //     var scaleHeight = height / designHeight;
        //     var scale = Math.min(scaleWidth, scaleHeight);
        //     var gameSize = new cc.Size(designWidth * scale, designHeight * scale);
        //     cc.view.resizeCanvas(gameSize, cc.ResolutionPolicy.NO_BORDER);
        // });

        this.name = "GameView"
        this.externalThis = this;
        let ZOrder = {
            ["bg"]      : 100,          // 
            ["board"]   : 150,          // 
            ["ctrl"]    : 200,          // 
            ["animate"] : 250,          // 
            ["mask"]    : 300,          // 
            ["reward"]  : 400           // 
        }
        this._gameCtrl = appFacade.retrieveProxy('GameProxy')
    
        // 
        this.shakyNode = new cc.Node('shakyNode');
        this.node.addChild(this.shakyNode)
    
        // 
        if (!this.bgNode) {
            this.bgNode = new cc.Node('bgNode');
            this.node.addChild(this.bgNode, ZOrder.bg)
        }

        // 
        if (!this.boardNode) {
            this.boardNode = new cc.Node('boardNode');
            this.node.addChild(this.boardNode, ZOrder.board)
        }

        // 
        if (!this.ctrlNode) {
            this.ctrlNode = new cc.Node('ctrlNode');
            this.node.addChild(this.ctrlNode, ZOrder.ctrl)
        }
    
