
let gameModel_520 = function () {
    this.initGameData()
};
gameModel_520.prototype = {
    constructor: gameModel_520,
    initGameData() {
        this._UnlockRewardID    = 255   //id
        this._CanBn             = false //BN
        this._isPlayWaitAni     = true  //wait
        this.wildList  = null;
        this._GiveAwayCount     = 0     //0
        this._recoveryFreeList  = []
        this._BnSenceId         = 255   //id0-3
        this._isBreakLine       = false
        this._CanFree           = false
        this._wildList          = []
    },

    ResetBNGameData(){
        this._CanBn                     = false   //BN
        this._VerifyMailBox             = 255     //
        this._BnSenceId                 = 255     //id0-3
        this._bRewardGoodChoose         = {}      //id
        this._RewardNum                 = 255     //BN
        this._SkullNum                  = 255     //
    },
    
};

module.exports = new gameModel_520();
