
module.exports = puremvc.define(
	{
		parent: puremvc.SimpleCommand
	},
	{
		execute: function (account, password) {
			let fishGlobalDataProxy = this.facade.retrieveProxy('FishGlobalDataProxy');
			let gameLobbyLoginProxy = this.facade.retrieveProxy('GameLobbyLoginProxy');

			if (fishGlobalDataProxy.getGameLobbyApp() != null && fishGlobalDataProxy.getGameLobbyApp().socket) {
				//	console.log("fishGlobalDataProxy.getGameLobbyApp", fishGlobalDataProxy.getGameLobbyApp().socket)
				return
			}

			let app = KBEngine.createApp();
			app.wssAddr = LobbyServerAddr;

			fishGlobalDataProxy.setGameLobbyApp(app);
			gameLobbyLoginProxy.registerNetInterface(app);
			console.log('GameLobbyLoginCommand reconnect', account, password);
			fishGlobalDataProxy.setUserInfo(account, password);
			gameLobbyLoginProxy.loginLobby(channelPlatformName, account, password);
		}
	},
	{
		NAME: 'GameLobbyLoginCommand'
	});


