
cc.Class({
	extends: cc.Component,

	properties: {
	},

	onLoad: function () {
		cc.dynamicAtlasManager.enabled = false;
		this.target = this.node.getComponent(cc.Sprite)
		this._material = this.target.getMaterial(0)
		this.m_rippleList = []
		this.node.on(cc.Node.EventType.TOUCH_END, this.onTouchEnded, this);
		this.node.on(cc.Node.EventType.TOUCH_CANCEL, this.onTouchEnded, this);
	},
	start: function () {
		this._material.setProperty('ripple_num', 0);
	},
	onTouchEnded(event) {
		let touch = event.touch;
		let pos = touch.getLocation();
		if (this.m_rippleList.length <= 8) {
			let rippleList = {}
			rippleList.centerCoordinate = cc.v2(((pos.x / cc.winSize.width - 0.5) * (cc.winSize.width / 1704) + 0.5) * 1.775, 1 - ((pos.y / cc.winSize.height - 0.5) * (cc.winSize.height / 960) + 0.5))
			rippleList.runtime = 0;
			rippleList.lifespan = 4.0;
			this.m_rippleList.push(rippleList)
		}
	},
	update: function (dt) {
		for (let i = this.m_rippleList.length - 1; i >= 0; i--) {
			this.m_rippleList[i].runtime += dt;
			if (this.m_rippleList[i].runtime >= this.m_rippleList[i].lifespan) {
				cc.js.array.fastRemoveAt(this.m_rippleList, i)
			}
		}
		this._material.effect.setProperty('ripple_num', this.m_rippleList.length);
		for (let i = this.m_rippleList.length - 1; i >= 0; i--) {
			switch (i) {
				case 0:
					this._material.setProperty('coor_center1', this.m_rippleList[i].centerCoordinate);
					this._material.setProperty('run_time1', this.m_rippleList[i].runtime);
					break;
				case 1:
					this._material.setProperty('coor_center2', this.m_rippleList[i].centerCoordinate);
					this._material.setProperty('run_time2', this.m_rippleList[i].runtime);
					break;
				case 2:
					this._material.setProperty('coor_center3', this.m_rippleList[i].centerCoordinate);
					this._material.setProperty('run_time3', this.m_rippleList[i].runtime);
					break;
				case 3:
					this._material.setProperty('coor_center4', this.m_rippleList[i].centerCoordinate);
					this._material.setProperty('run_time4', this.m_rippleList[i].runtime);
					break;
				case 4:
					this._material.setProperty('coor_center5', this.m_rippleList[i].centerCoordinate);
					this._material.setProperty('run_time5', this.m_rippleList[i].runtime);
					break;
				case 5:
					this._material.setProperty('coor_center6', this.m_rippleList[i].centerCoordinate);
					this._material.setProperty('run_time6', this.m_rippleList[i].runtime);
					break;
				case 6:
					this._material.setProperty('coor_center7', this.m_rippleList[i].centerCoordinate);
					this._material.setProperty('run_time7', this.m_rippleList[i].runtime);
					break;
				case 7:
					this._material.setProperty('coor_center8', this.m_rippleList[i].centerCoordinate);
					this._material.setProperty('run_time8', this.m_rippleList[i].runtime);
					break;
				default:
					break;
			}
		}
	}
});