var UIManager = require('gameUIManager');
cc.Class({
    extends: cc.Component,

    properties: {
        pageView: {
            default: null,
            type: cc.PageView
        },
        btnBack: {
            default: null,
            type: cc.Button
        },
        btn_left: {
            default: null,
            type: cc.Button
        },
        btn_right: {
            default: null,
            type: cc.Button
        },
    },

    init: function () {

    },

    onLoad: function () {

        this.btnBack.node.on('click', this.BackButtonClickEvent, this);
        this.pageView.setCurrentPageIndex(0)
        this.btn_left.node.on('click', this.LeftButtonClickEvent, this);
        this.btn_right.node.on('click', this.RightButtonClickEvent, this);
    },

    BackButtonClickEvent: function () {
        if (this.node.kindId){
            let uiManager = new UIManager(this.node.kindId)
            uiManager.closeUI(this.node.viewId)
        }else{
            cc.log("the kindId is null");
        }
    },

    LeftButtonClickEvent: function () {

        if (this.pageView.getCurrentPageIndex() == 0) {
            this.pageView.scrollToPage(5)
        }
        else{
            this.pageView.scrollToPage(this.pageView.getCurrentPageIndex() - 1)
        }

    },

    RightButtonClickEvent: function () {

        if (this.pageView.getCurrentPageIndex() == 5) {
            this.pageView.scrollToPage(0)
        }
        else{
            this.pageView.scrollToPage(this.pageView.getCurrentPageIndex() + 1)
        }

    },

    onDestroy: function () {

    },

  
});