let GameListData = require('GameListData');

module.exports = puremvc.define(
	{
		parent: puremvc.Proxy,
		constructor: function () {
			puremvc.Proxy.call(this);
		}
	},
	{

        GetGameItemInfo: function (kindId) {
            
            for (var i = 0, len = GameListData.GameItmeList.length; i < len; i++) {
                if(GameListData.GameItmeList[i].wKindID == kindId){
                    return GameListData.GameItmeList[i];
                }
            }
			return null;
		},

		

		SetGameItem: function (ItemTableData) {
            
            for (var i = 0, len = ItemTableData.length; i < len; i++) {
                if(ItemTableData[i].wKindID>0){
                    itemfind = this.GetGameItemInfo(ItemTableData[i].wKindID)
                    if(itemfind == null){
                        GameListData.GameItmeList.push(ItemTableData[i])
                    }
                }
            }
			return ItemTableData;
		},

		GetGameItmeList: function () {            
			return GameListData.GameItmeList;
		},       


	},
	{
		NAME: 'GameListDataProxy'
	});