
module.exports = puremvc.define(
    {
        parent : puremvc.Proxy,
        constructor : function() {
            puremvc.Proxy.call(this, this.constructor.NAME);
        }
    },
    {
        //
        _wTableCount: 0,
        _wChairCount: 0,
        _wServerType: 0,
        _dwServerRule: 0,

        //
        _roomUserList: new Map(),

        //
        _tableUserList: new Map(),
    
        //
        _tableStatus: [],
    
        //id
        _wTableID: 65535,
    
    
        //id
        _wChairID: 65535,
    
        //
        _cbGameStatus: 0,
    
        //
        _cbAllowLookon: 0,
    
        _bChangeDesk: false,
        _bSendOption: false,
        
        onRegister : function() {
    
        },

        /***/
        setTableCount:function(TableCount){
            this._wTableCount = TableCount
        },

        /***/
        getTableCount:function(){
            return this._wTableCount
        },

        /***/
        setChairCount:function(ChairCount){
            this._wChairCount = ChairCount
        },

        /***/
        getChairCount:function(){
            return this._wChairCount
        },

        setServerType:function(ServerType){
            this._wServerType = ServerType
        },

        getServerType:function(){
            return this._wServerType
        },

        setServerRule:function(ServerRule){
            this._dwServerRule = ServerRule
        },

        getServerRule:function(){
            return this._dwServerRule
        },

        /***/
        setwTableID:function(TableID){
            this._wTableID = TableID
        },

        /***/
        getwTableID:function(){
            return this._wTableID
        },

        /***/
        setwChairID:function(ChairID){
            this._wChairID = ChairID
        },
       
        /***/
        getwChairID:function(){
            return this._wChairID
        },

        /***/
        setGameStatus:function(GameStatus){
            this._cbGameStatus = GameStatus
        },
       
        /***/
        getGameStatus:function(){
            return this._cbGameStatus
        },

        /***/
        setAllowLookon:function(AllowLookon){
            this._cbAllowLookon = AllowLookon
        },
       
        /***/
        getAllowLookon:function(){
            return this._cbAllowLookon
        },


        /***/
        setTableStatusById:function(tableId,tableStatus){
            this._tableStatus[tableId] = tableStatus
        },

        /***/
        getTableStatusById:function(tableId){
            return this._tableStatus[tableId]
        },

        /**ItemUserID*/
        setRoomUserList:function(ItemUserID,userItem){
            this._roomUserList.set(ItemUserID, userItem);
        },

        /**ItemUserID*/
        getRoomUserList:function(ItemUserID){
            return this._roomUserList.get(ItemUserID);
        },

        /***/
        onRemoveUser:function(ItemUserID){
            this._roomUserList.delete(ItemUserID);
        },

        /***/
        onUpDataTableUser:function(tableid, chairid, useritem){
            let _tableid = tableid + 1;
            let _chairid = chairid + 1 ;
            if(!this._tableUserList.get(_tableid)){
                this._tableUserList.set(_tableid,new Map());
            };

            if (useritem) {
                this._tableUserList.get(_tableid).set(_chairid,useritem.get("dwUserID"));
            }else{
                this._tableUserList.get(_tableid).set(_chairid,null);
            };

        }, 

        
        setChangeDesk:function(isChangeDesk){
            this._bChangeDesk = isChangeDesk;
        },

        
        getChangeDesk:function(){
            return this._bChangeDesk;
        },

        setSendOption:function(isSendOption){
            this._bSendOption = isSendOption;
        },

        
        getSendOption:function(){
            return this._bSendOption;
        },

        
    },
    {
        NAME : 'GameTableDataManager'
    });