let datatypes = require('GameLobbyLoginProtocol').datatypes;
//var UIManager = require('UIManager');
module.exports = puremvc.define(
	{
		parent: puremvc.Proxy,
		constructor: function () {
			puremvc.Proxy.call(this);
		}
	},
	{
		onRegister: function () {
			this._globalData = this.facade.retrieveProxy('FishGlobalDataProxy');
			this._gameListData = this.facade.retrieveProxy('GameListDataProxy');
		},

		registerNetInterface: function (app) {
			this.app = app
			let net = this.app.net;
			//
			//net.registerClient(this, 'loginLobby', datatypes.CMD_GP_LogonByAccounts);

			//
			net.registerClient(this, 'loginLobby', datatypes.CMD_GP_LogonByPhoneNum);

			// 
			net.registerClient(this, 'register', datatypes.CMD_GP_H5RegisterAccounts);

			// -
			net.registerServer(this, 'loginSuccess', datatypes.CMD_GP_LogonSuccess);
			net.registerServer(this, 'loginFinish', datatypes.CMD_GP_LobbyServerAddr);
			net.registerServer(this, 'loginError', datatypes.CMD_GP_LogonError);
			// -
			net.registerServer(this, 'gpLuaGameKind', datatypes.tagLuaGameKind);
			net.registerServer(this, 'gpListType', datatypes.tagGameType);
			net.registerServer(this, 'gpListKind', datatypes.tagGameKind);
			net.registerServer(this, 'gpListServer', datatypes.tagGameServer);
			net.registerServer(this, 'gpListFinish', datatypes.listFinish);
		},

		register: function (account, password) {
			let app = this.app;
			var wxUserInfo = this._globalData.getWXUserInfo();
			var headImgUrl = wxUserInfo.headImgUrl;
			var openId = wxUserInfo.openId;
			var unionId = wxUserInfo.unionId;
			var gender = wxUserInfo.gender;
			var nickName = wxUserInfo.nickName;
			let inviteUserID = this._globalData.getInviteUserID();
			app.net.callServer('register', channelID, headImgUrl, openId, unionId, account, password, gender, inviteUserID, ChannelPlatformType, nickName);
		},

		loginLobby: function (szChannelPlatformName, account, password) {
			let inviteUserID = this._globalData.getInviteUserID();
			let app = this.app;
			app.connect(function () {
				//app.net.callServer('loginLobby', channelID, account, password, inviteUserID, ChannelPlatformType, szChannelPlatformName);
				app.net.callServer('loginLobby', 1,1, channelID.toString(), "17604010279", "1233", 1, "test", "test", "test", "test", "test");
				heartbeat.startHeartbeat(function () {
					var appFacade = require('FishFacade');
					let account = appFacade.retrieveProxy('FishGlobalDataProxy').getAccount();
					let password = appFacade.retrieveProxy('FishGlobalDataProxy').getPassword();
					appFacade.sendNotification(FishConstants.LOGIN_LOBBY, account, password);
				})

				
				app.setOncloseCallback(function () {
					heartbeat.connected = false
				});
			});
		},

		loginSuccess: function (logonSuccessData) {
			cc.log('logonSuccess', JSON.stringify(logonSuccessData));
			this._globalData.setLogonSuccessData(logonSuccessData)
			this._globalData.setUserId(logonSuccessData.dwUserID);
			if (cc.sys.isNative && cc.sys.os == cc.sys.OS_IOS) {
				if (logonSuccessData.szUnionID && logonSuccessData.szUnionID.length > 5) {
					this._globalData.setUserInfo(logonSuccessData.szUnionID, logonSuccessData.szUnionID);
				}
			}
		},

		loginFinish: function (data) {
			cc.log('logonFinish', data);
			//KBEngine.destroyApp(this.app);
			//this.app = null;
			//this.sendNotification(FishConstants.LOGIN_LOBBY_FINISH);

			let account = this._globalData.getAccount();
			let password = this._globalData.getPassword();
			//UIManager.saveAccountData(account, password)
			if (!hideExchangeRedPacket2) {
				//UIManager.openUI(124)
			}
		},

		loginError: function (errorCode, errorMsg) {
			let account = this._globalData.getAccount();
			let password = this._globalData.getPassword();
			cc.log('logonError', errorCode, errorMsg);
			if (errorCode == 101) {   //
				this.register(account, password);
			} else {
				// 
				KBEngine.destroyApp(this.app);
				cc.director.loadScene('GameLobbyLoginScene');
				// UIManager.openTip(errorMsg)
				// UIManager.saveAccountData('', '')
			}

		},

		gpLuaGameKind: function (ItemTableData) {
			cc.log('gpLuaGameKind:', ItemTableData);
			this._gameListData.SetGameItem(ItemTableData);
		},

		gpListType: function (pGameType) {
			cc.log('gpListType', pGameType);
		},

		gpListKind: function (pGameKind) {
			cc.log('gpListKind', pGameKind);
		},

		gpListServer: function (pGameServer) {
			cc.log('gpListServer', pGameServer);
			let fishGameKind = this._globalData.getFishGameKind();
			if (pGameServer.wKindID === fishGameKind) {
				this._globalData.getGameServerInfo()[pGameServer.wServerID] = pGameServer
				this._globalData.setFishServerIpPort(pGameServer.szServerAddr, pGameServer.wServerPort);
			}
		},

		gpListFinish: function () {
			cc.log('gpListFinish');
			// this.sendNotification(FishConstants.LOGIN_LOBBY_FINISH);
			cc.log("enter game" + this._gameListData.GetGameItmeList()[0].wKindID)

			this.sendNotification(FishConstants.ENTER_GAME_SCENCE,this._gameListData.GetGameItmeList()[0],this.app);
			cc.director.loadScene('GameLoadingScene_' + this._gameListData.GetGameItmeList()[0].wKindID);
			let account = this._globalData.getAccount();
			let password = this._globalData.getPassword();
			//UIManager.saveAccountData(account, password)
		},

	},
	{
		NAME: 'GameLobbyLoginProxy'
	});